import * as React from "react";
import styles from "./category-card-component.module.scss";
import { Link } from "react-router-dom";
import { CategoryCard } from "@app/api";
import ArrowRight from "@assets/icons/arrow-right.svg";
import { IconComponent } from "@app/modules/core";

export interface ICategoryCardComponentProps {
  categoryCard: CategoryCard;
}

const CategoryCardComponent = (props: ICategoryCardComponentProps) => {
  const getRoute = (pathname: string) => props.categoryCard.link + pathname;

  return (
    <Link
      to={getRoute(props.categoryCard.category)}
      className={`${styles[`card`]} ${styles[`card--modify`]} ${styles[`card--category`]} `}
    >
      <div className={styles[`card__body`]}>
        <div className={styles[`card__head`]}>
          <div className={styles[`card__title`]}>
            <h4>{props.categoryCard.title}</h4>
          </div>
        </div>
        <div className={styles[`card__bottom`]}>
          <span className="button button--default">
            {props.categoryCard.buttonText}
            <IconComponent icon={ArrowRight} size="14px" />
          </span>
        </div>
      </div>
    </Link>
  );
};

export { CategoryCardComponent };
