import { ToggleModule } from "@app/api";
import { ISidebarItem } from "../core/sidebar-menu/sidebar-menu";

const toggleModulesFilteredByText = (text: string, arrayToBeFiltered: ToggleModule[]): ToggleModule[] => {
  const textUppercase = text.toUpperCase();
  const filteredByText: ToggleModule[] = [];
  if (arrayToBeFiltered.length > 0) {
    if (!textUppercase.trim()) {
      return arrayToBeFiltered;
    }
    arrayToBeFiltered.forEach(toggle => {
      if (toggle.title.toUpperCase().includes(textUppercase)) {
        filteredByText.push(toggle);
      } else {
        const foundItem: ToggleModule = {
          title: toggle.title,
          items: []
        };
        foundItem.items = toggle.items.filter(
          item => item.title.toUpperCase().includes(textUppercase) || item.content.toUpperCase().includes(textUppercase)
        );
        if (foundItem.items.length > 0) {
          filteredByText.push(foundItem);
        }
      }
    });
  }

  return filteredByText;
};

const filterByTitleKey = (key: number, sidebarItems: ISidebarItem[], itemsToFilter: ToggleModule[]): ToggleModule[] => {
  if (key === 0) {
    return itemsToFilter;
  }
  const filtered: ToggleModule[] = [];
  const title = sidebarItems[key].text;
  itemsToFilter.forEach(item => {
    if (item.title === title) {
      filtered.push(item);
    }
  });

  return filtered;
};

const filterByTitleKeyAndText = (
  titleKey: number,
  text: string,
  sidebarItems: ISidebarItem[],
  itemsToFilter: ToggleModule[]
): ToggleModule[] => {
  const filteredByTitle = filterByTitleKey(titleKey, sidebarItems, itemsToFilter);
  const filteredByText = toggleModulesFilteredByText(text, filteredByTitle);
  if (titleKey === 0) {
    // if there is no filter selected on the sidebar
    return filteredByText;
  }

  return toggleModulesFilteredByText(text, filteredByTitle);
};

const fillAndRemapSideBarItems = (originalItems: ToggleModule[]): ISidebarItem[] => {
  let fillSidebarItems: ISidebarItem[] = [];
  fillSidebarItems.push({ text: "Alle onderwerpen", id: 0 });
  const mappedArray = originalItems.map((item, index) => ({ text: item.title, id: index + 1 }));

  fillSidebarItems = fillSidebarItems.concat(mappedArray);

  return fillSidebarItems;
};

export { toggleModulesFilteredByText, filterByTitleKey, filterByTitleKeyAndText, fillAndRemapSideBarItems };
