import * as React from "react";
import { withFormik, FormikProps, FormikErrors, Form, Field } from "formik";
import styles from "./contact-form-component.module.scss";
import { Button, ResourceTextComponent } from "@app/modules/core";
import { Link } from "react-router-dom";
import { validateEmail } from "@app/util";

export interface IContactFormValues {
  firstName: string;
  lastName: string;
  emailAddress: string;
  companyString: string;
  phoneNumber: string;
  message: string;
}

interface IOtherProps {
  privacyText: string;
  privacyLink: string;
}

const InnerForm = (props: IOtherProps & FormikProps<IContactFormValues>) => {
  const { touched, errors } = props;

  return (
    <Form>
      <div className="form__row">
        <Field
          type="text"
          name="firstName"
          placeholder="Naam"
          className={`form__input ${touched.firstName && errors.firstName && styles["input_error"]}`}
        />
        {touched.firstName && errors.firstName && <div className={styles["error"]}>{errors.firstName}</div>}
      </div>
      <div className="form__row">
        <Field
          type="text"
          name="lastName"
          placeholder="Achternaam"
          className={`form__input ${touched.lastName && errors.lastName && styles["input_error"]}`}
        />
        {touched.lastName && errors.lastName && <div className={styles["error"]}>{errors.lastName}</div>}
      </div>
      <div className="form__row">
        <Field
          type="email"
          name="emailAddress"
          placeholder="Email adres"
          className={`form__input ${touched.emailAddress && errors.emailAddress && styles["input_error"]}`}
        />
        {touched.emailAddress && errors.emailAddress && <div className={styles["error"]}>{errors.emailAddress}</div>}
      </div>
      <div className="form__row">
        <Field type="text" name="companyString" placeholder="Bedrijfsnaam" className="form__input" />
      </div>
      <div className="form__row">
        <Field type="text" name="phoneNumber" placeholder="Telefoonnummer" className="form__input" />
      </div>
      <div className="form__row">
        <Field
          component="textarea"
          name="message"
          placeholder="Bericht"
          className={`form__textarea ${touched.message && errors.message && styles["input_error"]}`}
        />
        {touched.message && errors.message && <div className={styles["error"]}>{errors.message}</div>}
      </div>
      <div className="form__row">
        <div className="form__text">
          {props.privacyText}&nbsp;
          <Link to={props.privacyLink} target="_blank">
            <ResourceTextComponent resourceKey="contact-privacy-policy" />
          </Link>
        </div>
      </div>
      <div className="form__row form__row--footer">
        <Button title="Verzenden" variant="primary" buttonType="submit" />
      </div>
    </Form>
  );
};

interface IFormProps {
  onSubmit: (values: IContactFormValues) => void;
  privacyText: string;
  privacyLink: string;
}

export const ContactFormComponent = withFormik<IFormProps, IContactFormValues>({
  mapPropsToValues: () => ({
    firstName: "",
    lastName: "",
    emailAddress: "",
    companyString: "",
    phoneNumber: "",
    message: ""
  }),

  validate: (values: IContactFormValues) => {
    const errors: FormikErrors<IContactFormValues> = {};
    if (!values.firstName) {
      errors.firstName = "Vul uw voornaam in";
    }
    if (!values.lastName) {
      errors.lastName = "Vul uw achternaam in";
    }
    if (!values.emailAddress) {
      errors.emailAddress = "Vul uw e-mail adres in";
    } else if (!validateEmail(values.emailAddress)) {
      errors.emailAddress = "Geen valide e-mail adres";
    }
    if (!values.message) {
      errors.message = "Laat een bericht achter";
    }

    return errors;
  },

  handleSubmit: (values, bag) => {
    bag.props.onSubmit(values);
  }
})(InnerForm);
