import * as React from "react";
import { RouteComponentProps } from "react-router-dom";

import { SearchForm, ResourceTextComponent, IconComponent } from "@app/modules/core";
import { ItemCard } from "@app/modules/core/item-card";
import { Pagination } from "@app/util/pagination";
import { ItemOverviewContainerProps } from "./containers";
import style from "./item-overview-component.module.scss";
import { ItemOverviewModule } from "@app/api";

export interface IItemOverviewComponentProps {
  itemOverviewModule: ItemOverviewModule;
}

const ItemOverviewComponent = (
  props: IItemOverviewComponentProps & ItemOverviewContainerProps & RouteComponentProps
) => {
  const TAKE = 9;
  const [currentFilter, setCurrentFilter] = React.useState("");

  React.useEffect(() => {
    const totalPages = props.itemOverviewModule.state.totalItems / TAKE;
    props.setCards(props.itemOverviewModule.state.cards);
    props.setCurrentPage(1);
    props.setTotalPage(totalPages);
  }, []);

  function changePage(pageNumber: number) {
    props.getCards(TAKE, pageNumber, currentFilter);
  }

  function applyFilter(keyword: string) {
    setCurrentFilter(keyword);
    props.getCards(TAKE, 1, keyword);
  }

  return (
    <div className={style["item-overview"]}>
      <div className={style["item-overview__filters"]}>
        <div>
          {/* Implement Forms Select Component */}
          <select className="form__select">
            <option aria-selected>
              Sorteer op productlijn
              {/* <ResourceTextComponent resourceKey="item_overview_sortByProductLine" /> */}
            </option>
            <option aria-selected>Other items</option>
          </select>
        </div>
        <div>
          {/* placeholder needs to be a resource */}
          <SearchForm onChange={applyFilter} placeholder="Zoek in nieuws" />
        </div>
      </div>
      <div className={style["item-overview__posts"]}>
        {props.cards.map(card => (
          <ItemCard
            key={card.id}
            labels={card.cardLabels.map(item => (
              <div>
                <IconComponent icon={item.icon} size="14px" /> {item.title}
              </div>
            ))}
            link={card.URL}
            title={card.title}
            image={card.picture}
            category={card.category}
          />
        ))}
      </div>
      {props.cards.length === 0 ? (
        <div className={style["empty-state"]}>
          <h3>
            <ResourceTextComponent resourceKey="search-empty-state" />{" "}
          </h3>
        </div>
      ) : (
        <div className={style["item-overview__pagination"]}>
          <Pagination currentPage={props.currentPageNumber} changePage={changePage} totalItems={props.totalPages} />
        </div>
      )}
    </div>
  );
};

export { ItemOverviewComponent };
