import * as React from "react";
import { Recipe } from "@app/api";
import { WidgetItemsComponent, IWidgetItem } from "@app/modules/core/widget-items/widget-items.component";

export interface IRecipesSidebarWidgetComponentProps {
  recipes: Recipe[];
}
const convertRecipesItems = (recipes: Recipe[]): IWidgetItem[] =>
  recipes.map(item => ({
    imageUrl: item.imageUrl,
    imageLinkUrl: item.link,
    content: item.content
  }));

const RecipesSidebarWidgetsComponent = (props: IRecipesSidebarWidgetComponentProps) => (
  <WidgetItemsComponent
    header="Recepten"
    readMoreLinkName="Meer recepten"
    readMoreLinkUrl="/Recipes-overview"
    widgetItems={convertRecipesItems(props.recipes)}
  />
);

export { RecipesSidebarWidgetsComponent };
