import React, { useState, useEffect } from "react";
import styles from "./product-overview-simple-component.module.scss";
import { RouteComponentProps } from "react-router-dom";
import { ProductCardComponent } from "../../core/product-card";
import { SidebarComponent } from "../../product-line-filter/components";
import { FilterOptions, ProductOverviewSimpleModule } from "@app/api";
import { ProductOverviewSimpleContainerProps } from "../container";
import { Pagination } from "@app/util/pagination";
import { isQuickFilterEmpty } from "@app/util/product-filter";
export interface IProductOverviewSimpleComponentProps {
  productOverview: ProductOverviewSimpleModule;
}

const ProductOverviewSimpleComponent = (
  props: IProductOverviewSimpleComponentProps & ProductOverviewSimpleContainerProps & RouteComponentProps
) => {
  let URLParam = "";
  if (props.match) {
    URLParam = Object.values(props.match.params).toString();
  }

  const [quickFilterOptions, setQuickFilterOptions] = useState<FilterOptions[]>([]);
  const productPerPage = 12;
  const [currentPage, setCurrentPage] = useState(1);
  const [quickFilterEmpty, setQuickFilterEmpty] = useState(true);
  const [search, setSearch] = useState<string>("");
  function isSearchInputEmpty() {
    if (search) {
      props.searchProductsCards(search);
    } else {
      changePage(currentPage);
    }
  }
  function setInitialState() {
    changePage(currentPage);
    setQuickFilterOptions(props.productOverview.filter.quickFilterOptions);
  }

  useEffect(() => {
    setInitialState();
  }, []);

  useEffect(() => {
    applyQuickFilter(quickFilterOptions);
    if (quickFilterEmpty) {
      props.getProductsCards(URLParam, [], currentPage, productPerPage);
    }
  }, [quickFilterOptions]);

  useEffect(() => {
    isSearchInputEmpty();
  }, [search]);

  function changePage(pageNumber: number) {
    props.getProductsCards(URLParam, [], pageNumber, productPerPage);
    setCurrentPage(pageNumber);
  }

  function onFilterChanged(id: string): FilterOptions[] {
    const newFilterOptions = quickFilterOptions.map(option => {
      if (option.content === id) {
        option.value = !option.value;
      }

      return option;
    });
    setQuickFilterOptions(newFilterOptions);
    const isEmpty = isQuickFilterEmpty(newFilterOptions);
    setQuickFilterEmpty(isEmpty);

    return quickFilterOptions;
  }

  function applyQuickFilter(filterOptions: FilterOptions[]) {
    if (!quickFilterEmpty) {
      const list: string[] = [];
      filterOptions.forEach(option => {
        if (option.value === true) {
          list.push(option.content);
        }
      });
      props.getProductsCards(URLParam, list, currentPage, productPerPage);
    } else {
      setQuickFilterEmpty(true);
    }
  }

  return (
    <div className={styles[`product-overview`]}>
      <div className="uk-container">
        <div className="uk-grid uk-grid-medium">
          <div className="uk-width-1-4@m uk-width-1-3@s">
            <SidebarComponent
              quickFilterOptions={quickFilterOptions}
              onQuickFilterChanged={onFilterChanged}
              productFilterOptions={[]}
              onProductFilterChanged={onFilterChanged}
              relatedNewsItems={props.productOverview.filter.relatedNewsItems}
              relatedRecipeItems={props.productOverview.filter.relatedRecipeItems}
              onSearchFormChanged={product => {
                setSearch(product);
              }}
              quickFilterTitle={props.productOverview.filter.quickFilterTitle}
              productFilterTitle={props.productOverview.filter.productFilterTitle}
              showMoreButton={props.productOverview.filter.showMoreButton}
              productFilterMax={props.productOverview.filter.productFilterMax}
              quickFilterMax={props.productOverview.filter.quickFilterMax}
            />
          </div>
          <div className="uk-width-3-4@m uk-width-2-3@s">
            <div className={styles[`product-overview__wrap`]}>
              <div className="uk-grid uk-grid-small product-group__posts uk-child-width-1-2@s uk-child-width-1-3@m uk-child-width-1-4@l">
                {props.productCardList &&
                  props.productCardList.map((cards, count) => (
                    <div key={count}>
                      <ProductCardComponent productCard={cards} />
                    </div>
                  ))}
              </div>
              {props.productNumber === 0 && (
                <div className={styles[`empty-state`]}>
                  <h3>No results for your search</h3>
                </div>
              )}
              {props.productNumber >= productPerPage && (
                <Pagination currentPage={currentPage} changePage={changePage} totalItems={props.totalItems} />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export { ProductOverviewSimpleComponent };
