import * as React from "react";
import styles from "./footer-component.module.scss";
import { Link } from "react-router-dom";
import { FooterModule, SocialMediaButtons } from "@app/api";
import { TopFooterComponent } from "./top-footer.component";
import { SocialMediaComponent, SocialMediaComponentType } from "@app/modules/social-media";

export interface IFooterComponentProps {
  footerModule: FooterModule;
  socialMediaButtons: SocialMediaButtons;
}

const FooterComponent = (props: IFooterComponentProps) => (
  <div className={styles[`footer`]}>
    <div className="uk-container">
      <TopFooterComponent footerModule={props.footerModule} />
      <div className={styles[`footer__bottom`]}>
        <div className={styles[`footer__bottom-left`]}>
          <div className={`${styles[`footer__logo`]} uk-visible@s`}>
            <img src={props.footerModule.logo} alt="image" />
          </div>
          <ul className={styles[`footer__nav`]}>
            {props.footerModule.footerLinks.map((links, index) => (
              <li key={index}>
                <Link to={links.route}>
                  <span>{links.content}</span>
                </Link>
              </li>
            ))}
          </ul>
        </div>

        <SocialMediaComponent
          socialMediaButtons={props.socialMediaButtons}
          socialMediaComponentType={SocialMediaComponentType.LINK}
        />
      </div>
    </div>
  </div>
);

export { FooterComponent };
