import * as React from "react";
import styles from "./faq-overview-component.module.scss";
import { SearchForm, ResourceTextComponent } from "@app/modules/core";
import { FAQOverviewModule, ToggleModule } from "@app/api";
import { ToggleComponent } from "../toggle";
import { SidebarMenuComponent, ISidebarItem } from "../core/sidebar-menu/sidebar-menu";
import { filterByTitleKey, filterByTitleKeyAndText, fillAndRemapSideBarItems } from "./filter-methods";

export interface IFaqOverviewComponentProps {
  faqOverviewModule: FAQOverviewModule;
}

const FaqOverviewComponent = (props: IFaqOverviewComponentProps) => {
  const [filteredItems, setFilteredItems] = React.useState<ToggleModule[]>([]);
  const [sidebarItems, setSidebarItems] = React.useState<ISidebarItem[]>([]);
  const [currentFilterKey, setCurrentFilterKey] = React.useState<number>(0);
  const [currentFilterText, setCurrentFilterText] = React.useState<string>("");
  const style = {
    backgroundColor: "#f5fafa"
  };

  /**
   *
   * @param key index of the position in the sideBarItems to be selected
   */
  const changeCurrentFilterCategory = (key: number, currentTextFilter: string) => {
    setCurrentFilterKey(key);
    const newFilteredItems = filterByTitleKeyAndText(
      key,
      currentTextFilter,
      [...sidebarItems],
      [...props.faqOverviewModule.groups]
    );
    setFilteredItems(newFilteredItems);
  };

  const onSidebarItemChanged = (key: number) => {
    setCurrentFilterKey(key);
    const newFilteredItems = filterByTitleKeyAndText(
      key,
      currentFilterText,
      [...sidebarItems],
      [...props.faqOverviewModule.groups]
    );
    setFilteredItems(newFilteredItems);
  };

  const searchOnChange = (text: string) => {
    setCurrentFilterText(text);
    changeCurrentFilterCategory(0, text);
  };

  React.useEffect(() => {
    const mappedArray = fillAndRemapSideBarItems(props.faqOverviewModule.groups);
    setSidebarItems(mappedArray);
    setFilteredItems(filterByTitleKey(0, sidebarItems, props.faqOverviewModule.groups));
  }, []);

  return (
    <div className={styles["faq-overview"]} style={style}>
      <div className="uk-container">
        <div className="uk-grid">
          <div className="uk-width-1-3@s">
            <div className={styles["faq-overview__sidebar"]}>
              <div className={styles["faq-overview__sidebar__search"]}>
                <SearchForm onChange={searchOnChange} placeholder="Zoek in onderwerpen" />
              </div>
              <div className={styles["faq-overview__sidebar__block"]}>
                <SidebarMenuComponent
                  selectedItem={currentFilterKey}
                  onChange={onSidebarItemChanged}
                  title={props.faqOverviewModule.sidebarTitle}
                  items={sidebarItems}
                />
              </div>
            </div>
          </div>
          <div className="uk-width-2-3@s">
            {filteredItems.length > 0 ? (
              filteredItems.map((item, key) => <ToggleComponent title={item.title} items={item.items} key={key} />)
            ) : (
              <div className={styles["empty-state"]}>
                <h3>
                  <ResourceTextComponent resourceKey="search-empty-state" />
                </h3>
                {currentFilterKey > 0 && (
                  <div>
                    <span>{`You are currently searching in "${sidebarItems[currentFilterKey].text}".`}</span>
                    <a
                      role="button"
                      onClick={() => {
                        onSidebarItemChanged(0);
                      }}
                    >
                      {`Search for "${currentFilterText}" in all categories instead.`}
                    </a>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export { FaqOverviewComponent };
