import * as React from "react";
import { RouteComponentProps } from "react-router";
import { PagebuilderContainerProps } from "../index";
import { getModule } from "@app/util/get-modules-from-page";
import { EmptyPageComponent } from "@app/modules/empty-page/empty-page.component";
import { WordPressPostModule } from "@app/api";
import { getConfig } from "@app/util";
import { DecorCurveComponent } from "@app/modules/core/decor-curve";

export interface IPagebuilderComponentProps {}

const Z_INDEX = 100;

const renderWordPressModules = (
  wordPressPostModules: WordPressPostModule[],
  routeProps: RouteComponentProps
): JSX.Element[] =>
  wordPressPostModules.map((wordPressModule, index) => (
    <div
      style={{
        marginTop: wordPressModule.topMargin,
        marginBottom: wordPressModule.bottomMargin,
        position: "relative",
        zIndex: wordPressModule.name === "Navbar" ? Z_INDEX : "unset"
      }}
      key={index}
    >
      {wordPressModule.curve && <DecorCurveComponent decorCurveModule={wordPressModule.curve} />}
      {getModule(wordPressModule)}
    </div>
  ));
const getRoute = (pathname: string) => getConfig().BASE_URL + pathname;

const PagebuilderComponent = (props: IPagebuilderComponentProps & PagebuilderContainerProps & RouteComponentProps) => {
  React.useEffect(() => {
    if (props.location) {
      props.getPage(getRoute(props.location.pathname));
    }
    // tslint:disable-next-line: align
  }, []);

  return (
    <React.Fragment>
      {!!props.currentPage ? (
        renderWordPressModules(props.currentPage.wordPressPostModules, { ...props })
      ) : (
        <EmptyPageComponent />
      )}
    </React.Fragment>
  );
};

export { PagebuilderComponent };
