import React, { useState, useEffect } from "react";

import styles from "./product-line-filter-component.module.scss";
import { FilterOptions, ProductLineSingleModule } from "@app/api";
import { SidebarComponent } from "./product-overview-sidebar.component";
import { ProductOverviewContainerProps } from "../container";
import { RouteComponentProps } from "react-router";
import { ProductLineCardsComponent } from "./product-line-cards.component";
import { isQuickFilterEmpty } from "@app/util/product-filter";

export interface IProductLineFilterComponentProps {
  productLine: ProductLineSingleModule;
}

const ProductLineFilterComponent = (
  props: IProductLineFilterComponentProps & ProductOverviewContainerProps & RouteComponentProps
) => {
  const [quickFilterOptions, setQuickFilterOptions] = useState<FilterOptions[]>([]);
  const [productFilterOptions, setProductFilterOptions] = useState<FilterOptions[]>([]);
  const [quickFilterEmpty, setQuickFilterEmpty] = useState(true);
  const [productFilterEmpty, setProductFilterEmpty] = useState(true);

  const [productList, setProductList] = useState<string[]>([]);
  const [search, setSearch] = useState<string>("");
  const [onSearch, setOnSearch] = useState(false);

  function isSearchInputEmpty() {
    if (search) {
      props.searchProductsCards(search);
      setOnSearch(true);
    } else {
      setOnSearch(false);
      props.setProductLineModule(props.productLine);
    }
  }
  function setInitialState() {
    if (props.productLine.filter.quickFilterOptions) {
      setQuickFilterOptions(props.productLine.filter.quickFilterOptions);
    }
    if (props.productLine.filter.productFilterOptions) {
      setProductFilterOptions(props.productLine.filter.productFilterOptions);
    }
    if (quickFilterEmpty && productFilterEmpty) {
      props.setProductLineModule(props.productLine);
    }
  }

  function applyProductFilter(filterOptions: FilterOptions[]) {
    if (!productFilterEmpty) {
      const list: string[] = [];
      filterOptions.forEach(option => {
        if (option.value === true) {
          list.push(option.content);
        }
      });
      props.getCategoryCards(productList, list);
    }
  }

  function applyQuickFilter(filterOptions: FilterOptions[]) {
    const list: string[] = [];
    filterOptions.forEach(option => {
      if (option.value === true) {
        list.push(option.content);
      }
    });
    if (productFilterEmpty) {
      props.getProductCards(list);
    } else {
      applyProductFilter(productFilterOptions);
    }
    setProductList(list);
  }

  useEffect(() => {
    setInitialState();
  }, []);

  useEffect(() => {
    isSearchInputEmpty();
  }, [search]);

  useEffect(() => {
    if (!quickFilterEmpty) {
      applyQuickFilter(quickFilterOptions);
    } else {
      setProductList([]);
    }

    applyProductFilter(productFilterOptions);
    setInitialState();
  }, [quickFilterOptions, productFilterOptions]);

  function onQuickFilterChanged(id: string): FilterOptions[] {
    const newFilterOptions = quickFilterOptions.map(option => {
      if (option.content === id) {
        option.value = !option.value;
      }

      return option;
    });
    setQuickFilterOptions(newFilterOptions);
    const isEmpty = isQuickFilterEmpty(newFilterOptions);
    setQuickFilterEmpty(isEmpty);

    return quickFilterOptions;
  }

  function onProductFilterChanged(id: string): FilterOptions[] {
    const newFilterOptions = productFilterOptions.map(option => {
      if (option.content === id) {
        option.value = !option.value;
      }

      return option;
    });

    setProductFilterOptions(newFilterOptions);
    isProductFilterEmpty();

    return productFilterOptions;
  }

  function isProductFilterEmpty() {
    let i = 0;
    productFilterOptions.forEach(options => {
      if (options.value) {
        i += 1;
      }
    });
    if (i !== 0) {
      setProductFilterEmpty(false);
    } else {
      setProductFilterEmpty(true);
    }
  }

  return (
    <div className={styles[`product-overview`]}>
      <div className={`${styles[`layout-two-columns`]} ${styles[`layout-two-columns--decor-bottom`]}`}>
        <div className={styles[`layout-two-columns__foreground`]}>
          <div className="uk-container">
            <div className="uk-grid uk-grid-medium">
              <div className="uk-width-1-4@m uk-width-1-3@s">
                <SidebarComponent
                  quickFilterOptions={quickFilterOptions}
                  onQuickFilterChanged={onQuickFilterChanged}
                  productFilterOptions={productFilterOptions}
                  onProductFilterChanged={onProductFilterChanged}
                  relatedNewsItems={props.productLine.filter.relatedNewsItems}
                  relatedRecipeItems={props.productLine.filter.relatedRecipeItems}
                  onSearchFormChanged={product => {
                    setSearch(product);
                  }}
                  quickFilterTitle={props.productLine.filter.quickFilterTitle}
                  productFilterTitle={props.productLine.filter.productFilterTitle}
                  showMoreButton={props.productLine.filter.showMoreButton}
                  productFilterMax={props.productLine.filter.productFilterMax}
                  quickFilterMax={props.productLine.filter.quickFilterMax}
                />
              </div>
              <div className="uk-width-3-4@m uk-width-2-3@s">
                <div className={styles[`product-overview__wrap`]}>
                  <ProductLineCardsComponent
                    categoryCardList={props.categoryCardList}
                    productCardList={props.productCardList}
                    productFilterEmpty={productFilterEmpty}
                    quickFilterEmpty={quickFilterEmpty}
                    onSearch={onSearch}
                  />
                  {(props.productNumber === 0 || props.categoryNumber === 0) && (
                    <div className={styles[`empty-state`]}>
                      <h3>No results for your search</h3>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export { ProductLineFilterComponent };
