import * as React from "react";
import style from "./related-post-card-component.module.scss";
import { Link } from "react-router-dom";
import classNames from "classnames";

interface IRelatedPostCardProps {
  link: string;
  title?: string;
  image?: string;
  content?: string;
  draggable?: boolean;
}

const RelatedPostCard = (props: IRelatedPostCardProps) => (
  <div style={{ position: "relative" }}>
    <div className={style["related-post-card__shadow"]} />
    <div className={style["related-post-card__holder"]}>
      <Link
        draggable={false}
        to={props.link}
        className={classNames(`${style["related-post-card"]} ${style["related-post-card--product"]}`)}
        onClick={e => {
          if (props.draggable) {
            e.preventDefault();
          }
        }}
        target="_blank"
      >
        <div className={style["related-post-card__body"]}>
          {props.image && (
            <div className={style["related-post-card__media"]}>
              <div className={style["related-post-card__image"]}>
                <img src={props.image} alt="RelatedPostCard onze-mensen" />
              </div>
            </div>
          )}
          <div className={style["related-post-card__body"]}>
            <div className={style["related-post-card__head"]}>
              <div className={style["related-post-card__title"]}>{props.title}</div>
              {props.content && <div className={style["related-post-card__content"]}>{props.content}</div>}
            </div>
          </div>
        </div>
      </Link>
    </div>
  </div>
);

export { RelatedPostCard };
