/* tslint:disable */
import React, { useState } from "react";
import { withScriptjs, withGoogleMap, GoogleMap, Marker, InfoWindow } from "react-google-maps";

import styles from "./google-maps-component.module.scss";
import SearchBox from "react-google-maps/lib/components/places/SearchBox";
import { Markers } from "@app/api";
import { ResourceTextComponent } from "@app/modules/core";

interface IExtendedMarker extends Markers {
  isOpen: boolean;
}

export interface IGoogleMapsComponentProps {
  googleMapURL: string;
  loadingElement: JSX.Element;
  containerElement: JSX.Element;
  mapElement: JSX.Element;
  mapTypeControl: boolean;
  markers: Markers[];
}

const mapReference: { map: any; searchBox: any } = { map: undefined, searchBox: undefined };

const GoogleMapsComponent = withScriptjs(
  withGoogleMap((props: IGoogleMapsComponentProps) => {
    // warning, only sets marker on constructor
    React.useEffect(() => {
      const mappedMarkers: IExtendedMarker[] = props.markers.map(marker => ({
        isOpen: false,
        ...marker
      }));
      setMarkers(mappedMarkers);
    }, []);

    const [bounds, setBounds] = useState(undefined);
    const [extendedMarkers, setMarkers] = useState<IExtendedMarker[]>([]);

    const handleToggle = (marker: IExtendedMarker) => {
      const newMarkers = extendedMarkers.map(extendedMarker => {
        extendedMarker.isOpen = extendedMarker.id === marker.id;

        return extendedMarker;
      });

      setMarkers(newMarkers);
    };

    return (
      <GoogleMap
        ref={onMapMounted}
        defaultZoom={8}
        defaultCenter={{ lat: 52.1326, lng: 5.2913 }}
        defaultOptions={{
          streetViewControl: false,
          scaleControl: false,
          mapTypeControl: false,
          panControl: false,
          rotateControl: false,
          fullscreenControl: false
        }}
        onBoundsChanged={() => {
          setBounds(mapReference.map.getBounds());
        }}
      >
        {/* Searchbox */}
        <SearchBox
          ref={onSearchBoxMounted}
          bounds={bounds}
          controlPosition={google.maps.ControlPosition.TOP_LEFT}
          onPlacesChanged={onPlacesChanged}
        >
          <input
            type="text"
            placeholder="Stad, postcode"
            style={{
              border: `1px solid transparent`,
              borderRadius: `3px`,
              boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`,
              boxSizing: `border-box`,
              fontFaimily: `JosefinSans`,
              fontSize: `14px`,
              height: `32px`,
              marginLeft: `16px`,
              marginTop: `16px`,
              minHeight: `45px`,
              outline: `none`,
              padding: `0 12px`,
              textOverflow: `ellipses`,
              width: `240px`
            }}
          />
        </SearchBox>
        {/* Markers */}
        {extendedMarkers.map((marker: IExtendedMarker, index: any) => (
          <Marker
            icon={{
              anchor: new google.maps.Point(12, 24),
              url: require("@assets/icons/marker.svg")
            }}
            position={{ lat: marker.location.latitude, lng: marker.location.longitude }}
            onClick={() => handleToggle(marker)}
            key={index}
          >
            {marker.isOpen && (
              <InfoWindow onCloseClick={() => handleToggle(marker)}>
                <div key={index} className="info-window">
                  <div className="uk-grid uk-grid-small uk-child-width-1-2@s">
                    <div>
                      <div className="info-window__title">
                        <ResourceTextComponent resourceKey="google-maps-contact-information" />
                      </div>
                      <div>
                        {marker.displayName}
                        <br />
                        {marker.streetNameAndNumber}
                        <br />
                        {marker.postalCode}
                        <br />
                        {marker.city}
                        <br />
                        <ResourceTextComponent resourceKey="google-maps-phone-number" /> {marker.phone}
                        <br />
                        <ResourceTextComponent resourceKey="google-maps-fax-number" /> {marker.fax}
                        <br />
                      </div>
                    </div>
                    <div>
                      <div className="info-window__title">
                        <ResourceTextComponent resourceKey="google-maps-opening-times" />
                      </div>
                      <ul className="info-window__list">
                        {marker.workingHours.map(workingHour => (
                          <li>
                            <span className="info-window__item">{workingHour.day}</span>
                            <span className="info-window__item">
                              <ResourceTextComponent
                                resourceKey="google-maps-times"
                                values={{ openingTime: workingHour.opensAt, closingTime: workingHour.closesAt }}
                              />
                            </span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </InfoWindow>
            )}
          </Marker>
        ))}
      </GoogleMap>
    );
  })
);

const onSearchBoxMounted = (ref: any) => {
  mapReference.searchBox = ref;
};

const onMapMounted = (ref: any) => {
  mapReference.map = ref;
};

const onPlacesChanged = () => {
  const places = mapReference.searchBox.getPlaces();
  const bounds = new google.maps.LatLngBounds();

  places.forEach(
    (place: {
      geometry: {
        viewport: google.maps.LatLngBounds | google.maps.LatLngBoundsLiteral;
        location: google.maps.LatLng | google.maps.LatLngLiteral;
      };
    }) => {
      if (place.geometry.viewport) {
        bounds.union(place.geometry.viewport);
      } else {
        bounds.extend(place.geometry.location);
      }
    }
  );
  // const nextMarkers = places.map((place: { geometry: { location: any; }; }) => ({
  //   position: place.geometry.location,
  // }));
  // const nextCenter = _.get(nextMarkers, '0.position', this.state.center);

  // this.setState({
  //   center: nextCenter,
  //   markers: nextMarkers,
  // });
  mapReference.map.fitBounds(bounds);
};

export { GoogleMapsComponent };
