import React from "react";
import { Button } from "@app/modules/core";
import { RelatedLinksModule } from "@app/api";

export interface IRelatedLinksComponentProps {
  relatedLinks: RelatedLinksModule;
}

const RelatedLinksComponent = (props: IRelatedLinksComponentProps) => (
  <div>
    <h2>{props.relatedLinks.title}</h2>
    <br />
    <div className="uk-grid uk-child-width-1-2@s uk-child-width-1-4@l">
      {props.relatedLinks.links.map(links => (
        <div>
          <h4>{links.title}</h4>
          <p>{links.content}</p>
          <Button href={links.button.route} variant="default" title={links.button.content} />
        </div>
      ))}
    </div>
  </div>
);

export { RelatedLinksComponent };
