import * as React from "react";
import styles from "./simple-card-component.module.scss";
import { Link } from "react-router-dom";

interface ISimpleCardProps {
  link: string;
  title?: string;
  image?: string;
  content?: JSX.Element;
  dragging?: boolean;
}
const SimpleCard = (props: ISimpleCardProps) => (
  <Link
    draggable={false}
    to={props.link}
    className={`${styles["simple-card"]} ${styles["simple-card--container"]}`}
    onClick={e => {
      if (props.dragging) {
        e.preventDefault();
      }
    }}
    target="_blank"
  >
    {props.image && (
      <div className={styles["simple-card__media"]}>
        <div className={styles["simple-card__image"]}>
          <img src={props.image} alt="Card onze-mensen" />
        </div>
        {props.content ? <div className={styles["simple-card__icon"]}>{props.content}</div> : ""}
      </div>
    )}
    <div className={styles["simple-card__body"]}>
      <div className={styles["simple-card__head"]}>
        <div className={styles["simple-card__title"]}>{props.title}</div>
      </div>
    </div>
  </Link>
);

export { SimpleCard };
