import { Dispatch } from "react-redux";
import { IAppState, cardActions } from "@app/stores";
import { CardApi, ItemOverviewStateModule } from "@app/api";

const getCards = (take: number, page: number, filter?: string) => {
  const skip = page * take - take;

  return async (dispatch: Dispatch<any>) => {
    cardActions.setCards({
      currentPageNumber: page,
      currentFilter: filter
    });
    try {
      const api = new CardApi();
      api
        .cardGet(take, skip, filter)
        .then(response => response.json())
        .then((response: ItemOverviewStateModule) => {
          let totalPages = response.totalItems / take;
          if (response.totalItems === 0) {
            totalPages = 0;
          }
          dispatch(
            cardActions.setCards({
              cards: response.cards,
              currentPageNumber: page,
              totalPages
            })
          );
        });
    } catch (error) {
      // TODO: error handling
    }
  };
};

export const cardThunks = {
  getCards
};
