import * as React from "react";
interface IMatchedLinkComponent {
  word: string;
  text: string;
  link: string;
}

const MatchedLinkComponent = (props: IMatchedLinkComponent) => {
  const [finalText, setFinalText] = React.useState<JSX.Element | undefined>(undefined);
  const highlighMatch = (word: string, text: string): void => {
    const START_MATCH = text.indexOf(word);
    const END_MATCH = START_MATCH + word.length;
    setFinalText(<p>{`${text.slice(0, START_MATCH)} <bold> ${word} </bold> ${text.slice(END_MATCH, text.length)}`}</p>);
  };
  React.useEffect(
    (): void => {
      highlighMatch(props.word, props.text);
    }
  );

  return <p>{finalText}</p>;
};

export { MatchedLinkComponent };
