import * as React from "react";

import style from "./menu-item-component.module.scss";
import classNames from "classnames";
import { Link } from "react-router-dom";
import { IconComponent } from "@app/modules/core";
import ArrowRight from "@assets/icons/arrow-right.svg";

export interface IMenuItemComponentProps {
  isActive?: boolean;
  title: string;
  id: number;
  onClick: (itemId: number, text: string) => void;
}

const MenuItemComponent = (props: IMenuItemComponentProps) => (
  <li
    className={classNames(`${style["menu-item"]}`, {
      [style["menu-item--active"]]: props.isActive
    })}
    key={props.id}
  >
    <Link
      onClick={() => {
        props.onClick(props.id, props.title);
      }}
      to="#"
    >
      <IconComponent icon={ArrowRight} size="14px" />
      <span>{props.title}</span>
    </Link>
  </li>
);

export { MenuItemComponent };
