import { ProductCard, CategoryCard, ProductLineFilter } from "@app/api";
import { REDUX } from "@app/constants/redux";
import { Action } from "redux";
import * as ActionType from "./product-line.types";

export interface IProductOverviewSimpleState {
  productCardList: ProductCard[];
  categoryCardList: CategoryCard[];
  categoryNumber: number;
  productNumber: number;
  totalItems: number;
}

const INITIAL_STATE: IProductOverviewSimpleState = {
  productCardList: [],
  categoryCardList: [],
  categoryNumber: 0,
  productNumber: 0,
  totalItems: 0
};

export const productOverviewSimpleReducer = (
  state: IProductOverviewSimpleState = INITIAL_STATE,
  action: Action
): IProductOverviewSimpleState => {
  switch (action.type) {
    case REDUX.PRODUCTLINE.SET_PRODUCT_LINE: {
      const { productCardList, categoryCardList, categoryNumber, productNumber } = <ActionType.ICategoryCards>action;

      return { ...state, productCardList, categoryCardList, categoryNumber, productNumber };
    }
    case REDUX.PRODUCTLINE.SET_PRODUCT_OVERVIEW_SIMPLE: {
      const { productCardList, totalItems, productNumber } = <ActionType.IProductOverviewSimples>action;

      return { ...state, productCardList, totalItems, productNumber };
    }

    default: {
      return state;
    }
  }
};
