import * as React from "react";
import { Link } from "react-router-dom";
import "./product-card.component.scss";
import { Button } from "@app/modules/core/button";
import ArrowRight from "@assets/icons/arrow-right.svg";
interface IProductCardProps {
  link: string;
  title?: string;
  image?: string;
  content?: string;
  buttonText?: string;
}

const ProductCard = (props: IProductCardProps) => (
  <Link to={props.link} className="product-card">
    {props.image && (
      <div className="product-card__media">
        <div className="product-card__image">
          <img src={props.image} alt="product" />
        </div>
      </div>
    )}
    <div className="product-card__body">
      <div className="product-card__holder">
        <div className="product-card__head">
          <div className="product-card__title">{props.title}</div>
          {props.content && <div className="product-card__content">{props.content}</div>}
        </div>
        <div className="product-card__content__button">
          <Button icon={ArrowRight} variant="default" title={props.buttonText} />
        </div>
      </div>
    </div>
  </Link>
);

export { ProductCard };
