import { REDUX } from "@app/constants";
import * as ActionType from "./card.types";

const setCards = (payload: ActionType.ICard) => ({
  type: REDUX.CARD.SET_CARDS,
  ...payload
});

const setCurrentPage = (payload: ActionType.ICurrentPage) => ({
  type: REDUX.CARD.SET_CURRENT_PAGE,
  ...payload
});

const setTotalPages = (payload: ActionType.ITotalPages) => ({
  type: REDUX.CARD.SET_CURRENT_PAGE,
  ...payload
});

export const cardActions = {
  setCards,
  setCurrentPage,
  setTotalPages
};
