import { ProductCard } from "@app/api";

import { MapStateToProps, MapDispatchToProps, connect } from "react-redux";

import { productLineThunks } from "@app/middleware/thunk/product-line.thunk";
import { IAppState } from "@app/stores";
import {
  IProductOverviewSimpleComponentProps,
  ProductOverviewSimpleComponent
} from "@app/modules/product-overview-simple/component";
import { withRouter } from "react-router";

interface IStateProps {
  productCardList: ProductCard[];
  totalItems: number;
  productNumber: number;
}

interface IDispatchProps {
  getProductsCards: (category: string, filter: string[], skip: number, take: number) => void;
  searchProductsCards: (filter: string) => void;
}

const mapStateToProps: MapStateToProps<IStateProps, IProductOverviewSimpleComponentProps, IAppState> = state => {
  const { productCardList, totalItems, productNumber } = state.productOverviewSimple;

  return { productCardList, totalItems, productNumber };
};

const mapDispatchToProps: MapDispatchToProps<IDispatchProps, IProductOverviewSimpleComponentProps> = dispatch => ({
  getProductsCards: (category: string, filter: string[], skip: number, take: number) =>
    dispatch(productLineThunks.getProductsWithPagination(category, filter, skip, take)),
  searchProductsCards: (filter: string) => dispatch(productLineThunks.searchProductsCards(filter))
});

export type ProductOverviewSimpleContainerProps = IStateProps & IDispatchProps;

export const ProductOverviewSimpleContainer = withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(ProductOverviewSimpleComponent)
);
