import { Action } from "redux";

import { REDUX } from "@app/constants";
import * as ActionType from "./card.types";
import { CardModel } from "@app/api";

export interface ICardState {
  cards: CardModel[];
  totalPages: number;
  currentPageNumber: number;
  currentFilter: string;
}

const INITIAL_STATE: ICardState = {
  cards: [],
  currentPageNumber: 1,
  totalPages: 0,
  currentFilter: ""
};

export const cardReducer = (state: ICardState = INITIAL_STATE, action: Action): ICardState => {
  switch (action.type) {
    case REDUX.CARD.SET_CARDS: {
      const { cards, totalPages, currentFilter, currentPageNumber } = <ActionType.ICard>action;
      let toReturn = { ...state };
      if (totalPages) {
        toReturn = { ...toReturn, totalPages };
      }
      if (currentPageNumber) {
        toReturn = { ...toReturn, currentPageNumber };
      }
      if (currentFilter) {
        toReturn = { ...toReturn, currentFilter };
      }
      if (cards) {
        toReturn = { ...toReturn, cards };
      }

      return toReturn;
    }
    case REDUX.CARD.SET_CURRENT_FILTER: {
      const { currentFilter } = <ActionType.ICard>action;

      return { ...state };
    }
    case REDUX.CARD.SET_CURRENT_PAGE: {
      const { currentPageNumber } = <ActionType.ICurrentPage>action;

      return { ...state, currentPageNumber };
    }
    default: {
      return state;
    }
  }
};
