import { REDUX } from "@app/constants";

import * as ActionType from "./product-line.types";

const setProductLine = (payload: ActionType.IProductLine) => ({
  type: REDUX.PRODUCTLINE.SET_PRODUCT_LINE,
  ...payload
});
const setCategoryCards = (payload: ActionType.ICategoryCards) => ({
  type: REDUX.PRODUCTLINE.SET_PRODUCT_LINE,
  ...payload
});
const setProductCards = (payload: ActionType.IProductCards) => ({
  type: REDUX.PRODUCTLINE.SET_PRODUCT_LINE,
  ...payload
});
const setProductOverviewSimple = (payload: ActionType.IProductOverviewSimples) => ({
  type: REDUX.PRODUCTLINE.SET_PRODUCT_OVERVIEW_SIMPLE,
  ...payload
});
const setCurrentPage = (payload: ActionType.ICurrentPage) => ({
  type: REDUX.PRODUCTLINE.SET_CURRENT_PAGE,
  ...payload
});
export const productLineActions = {
  setCategoryCards,
  setProductOverviewSimple,
  setCurrentPage,
  setProductCards,
  setProductLine
};
