import React from "react";
import Slider from "react-slick";
import { InstagramWalllModule, IntagramPosts } from "@app/api";
import styles from "./instagram-wall-component.module.scss";
import { SimpleCard } from "@app/modules/core/simple-card";
import { IconComponent } from "@app/modules/core";
import Instagram from "@assets/icons/instagram.svg";
import { getArrow } from "@app/constants/icons";

export interface IInstagramWallComponentProps {
  instagramWallModule: InstagramWalllModule;
  customNextArrow?: JSX.Element | undefined;
  customPrevArrow?: JSX.Element | undefined;
  infiniteScrolling?: boolean;
}

interface IBasicArrowProps {
  prev?: boolean;
  customArrow?: JSX.Element;
  onClick?: () => void;
  className?: string;
}

const BasicArrow = (props: IBasicArrowProps) => {
  const { className, onClick } = props;
  let result = null;
  const customStyle = "instagram__slick-arrow-" + `${props.prev ? "prev" : "next"}`;
  if (props.customArrow === undefined) {
    result = (
      <button
        className={`${className} ${styles["instagram__slick-arrow"]} ${styles[customStyle]}`}
        style={{ display: "block" }}
        onClick={onClick}
      >
        <img src={getArrow(props.prev)} alt="arrow" />
      </button>
    );
  } else {
    result = (
      <div className={className} onClick={onClick} role="button">
        {props.customArrow}
      </div>
    );
  }

  return result;
};

const convertInstagramPostsToCards = (posts: IntagramPosts[], width: number) => {
  const breakpointSinglePost = 768;
  const MAX_BREAKPOINT = 3;
  const tpl = width < breakpointSinglePost ? [1, 1] : [1, MAX_BREAKPOINT];
  let count = 0;
  const argsItems = [];
  for (let i = 0; i < posts.length; i += 1) {
    if (tpl[count] === 1) {
      argsItems.push(posts.slice(i, i + tpl[count]));
    } else {
      argsItems.push(posts.slice(i, i + tpl[count]));
    }
    i += tpl[count] - 1;
    if (tpl.length - 1 > count) {
      count += 1;
    } else {
      count = 0;
    }
  }

  return argsItems;
};
const breakpointShowTwoSlides = 1000;
const breakpointShowOneSlides = 639;
const scrollingSpeed = 500;
const settingInstagramWall = (
  infiniteScrolling: boolean,
  nextArrow?: JSX.Element | undefined,
  prevArrow?: JSX.Element | undefined
) => ({
  className: `${styles["instagram__slick-slider"]}`,
  arrows: true,
  nextArrow: <BasicArrow customArrow={nextArrow} />,
  prevArrow: <BasicArrow customArrow={prevArrow} prev />,
  dots: false,
  draggable: true,
  infinite: infiniteScrolling,
  responsive: [
    {
      breakpoint: breakpointShowTwoSlides,
      settings: {
        slidesToScroll: 1,
        slidesToShow: 2
      }
    },
    {
      breakpoint: breakpointShowOneSlides,
      settings: {
        slidesToScroll: 1,
        slidesToShow: 1
      }
    }
  ],
  slidesToScroll: 1,
  slidesToShow: 3,
  speed: scrollingSpeed
});

const InstagramWallComponent = (props: IInstagramWallComponentProps) => {
  const icon = <IconComponent icon={Instagram} size="36px" />;
  const [width, setWidth] = React.useState(0);
  const cards = convertInstagramPostsToCards(props.instagramWallModule.posts, width);
  const infiniteScrolling = props.infiniteScrolling || false;
  const settingsSlider = settingInstagramWall(infiniteScrolling, props.customNextArrow, props.customPrevArrow);
  React.useEffect(() => {
    setWidth(window.innerWidth);
    window.addEventListener("resize", () => {
      setWidth(window.innerWidth);
    });
  });

  return (
    <React.Fragment>
      <div className={`${styles["instagram-wall"]}`}>
        <div className="uk-container">
          <div style={{ marginTop: "6px" }} className={styles["instagram-wall__title"]}>
            <h2>{props.instagramWallModule.title}</h2>
          </div>
          <div className={styles["instagram-wall__body"]}>
            <Slider {...settingsSlider}>
              {cards.map((items, index) => (
                <div key={index} className={styles[items.length > 1 ? "instagram-wall__collection" : "big-card"]}>
                  {items.map((elements, count) => (
                    <SimpleCard key={count} link="/" image={elements.imageUrl} title={elements.title} content={icon} />
                  ))}
                </div>
              ))}
            </Slider>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export { InstagramWallComponent };
