import * as React from "react";

import style from "./sidebar-menu-component.module.scss";
import { MenuItemComponent } from "./menu-item/item-component";
interface ISidebarMenuProps {
  items: ISidebarItem[];
  title: string;
  onChange: (id: number) => void;
  selectedItem: number;
}

export interface ISidebarItem {
  text: string;
  id: number;
}

const SidebarMenuComponent = (props: ISidebarMenuProps) => {
  const items: ISidebarItem[] = props.items;
  const title: string = props.title;
  const menuItemClick = (itemId: number): void => {
    props.onChange(itemId);
  };

  return (
    <React.Fragment>
      {items && (
        <div className={style["sidebar-menu"]}>
          <div className={style["sidebar-menu__title"]}>
            <h4>{title}</h4>
          </div>
          <ul className={style["sidebar-menu__list"]}>
            {items.map((item: ISidebarItem, index: number) => (
              <MenuItemComponent
                key={index}
                isActive={item.id === props.selectedItem}
                onClick={menuItemClick}
                id={item.id}
                title={item.text}
              />
            ))}
          </ul>
        </div>
      )}
    </React.Fragment>
  );
};
export { SidebarMenuComponent };
