import * as React from "react";
import style from "./toggle-component.module.scss";
import classNames from "classnames";
import { ExpandablePanelComponent } from "../core/expandable-panel";
import { ExpandablePanel } from "@app/api";

export interface IToggleComponentProps {
  items: ExpandablePanel[];
  title: string;
}

const ToggleComponent = (props: IToggleComponentProps) => {
  const [openedPanel, setOpenedPanel] = React.useState(-1);

  function panelClicked(key: number) {
    if (openedPanel === key) {
      setOpenedPanel(-1);
    } else {
      setOpenedPanel(key);
    }
  }

  return (
    <div className={classNames(`${style["toggle"]}`)}>
      <div className={style["toggle__title"]}>
        <h2>{props.title}</h2>
      </div>
      <div style={{ margin: 0 }} className="uk-container uk-container-small">
        <div className={style["toggle__block"]}>
          {props.items.map((item, key) => (
            <ExpandablePanelComponent
              callback={panelClicked}
              identifier={key}
              opened={openedPanel === key}
              key={key}
              title={item.title}
            >
              <span>{item.content}</span>
            </ExpandablePanelComponent>
          ))}
        </div>
      </div>
    </div>
  );
};

export { ToggleComponent };
