import * as React from "react";
import { SearchForm, ResourceTextComponent } from "@app/modules/core";
import "./product-line-overview.component.scss";
import { ProductCard } from "./../card";

import { ProductLineOverviewModule, ProductLineOverviewCard } from "@app/api";

export interface IProductLineOverviewComponentProps {
  productLineOverviewModule: ProductLineOverviewModule;
}
interface IProductLineArrayCard {
  card: ProductLineOverviewCard[];
  floatRight: boolean;
}

const ProductsLineOverviewComponent = (props: IProductLineOverviewComponentProps) => {
  const [orderedItems, setOrderedItems] = React.useState<IProductLineArrayCard[]>([]);

  const remapArray = (arrayToRemap: ProductLineOverviewCard[]): IProductLineArrayCard[] => {
    const arrayLength = arrayToRemap.length;
    let floatRight = true;
    const arrays: IProductLineArrayCard[] = [];
    const ITEMS_PER_GROUP = 7;
    for (let i = 0; i < arrayLength; i += ITEMS_PER_GROUP) {
      const innerArray = arrayToRemap.slice(i, i + ITEMS_PER_GROUP);
      arrays.push({
        floatRight,
        card: innerArray
      });
      floatRight = !floatRight;
    }

    return arrays;
  };

  React.useEffect(() => {
    setOrderedItems(remapArray(props.productLineOverviewModule.productsLine));
  }, [props.productLineOverviewModule.productsLine]);

  const filterProductLineCardsByText = (filter: string): ProductLineOverviewCard[] => {
    const cardList = props.productLineOverviewModule.productsLine;

    if (filter) {
      const filterCapitalized = filter.toUpperCase();

      return cardList.filter(
        card =>
          card.title.toUpperCase().includes(filterCapitalized) || card.content.toUpperCase().includes(filterCapitalized)
      );
    }

    return cardList;
  };

  const onSearchInputChange = (text: string) => {
    const filteredProductLineCards = filterProductLineCardsByText(text);
    const mappedArray = remapArray([...filteredProductLineCards]);
    setOrderedItems(mappedArray);
  };

  return (
    <div className="products-line-overview2">
      <div className="uk-container">
        <div className="products-line-overview2__filters">
          <div>
            <SearchForm onChange={onSearchInputChange} placeholder="Zoek je product" />
          </div>
          <div>
            <select className="form__select">
              <option aria-selected>Productcategorieën</option>
              <option aria-selected>Item</option>
            </select>
          </div>
          <div>
            <select className="form__select">
              <option aria-selected>Voedingspatroon</option>
              <option aria-selected>Item</option>
            </select>
          </div>
        </div>
        {orderedItems.length > 0 ? (
          orderedItems.map((item, index) => (
            <div style={{ marginTop: "32px" }} key={index} className="products-line-overview2__list">
              {item.card.map((card, key) => (
                <div
                  className={`products-line-overview2__list-item ${item.floatRight ? "lessThan7" : "moreThan7"} `}
                  key={key}
                >
                  <ProductCard
                    link="/product-line-single"
                    title={card.title}
                    image={card.image}
                    content={card.content}
                    buttonText={card.buttonText}
                  />
                </div>
              ))}
            </div>
          ))
        ) : (
          <div className="empty-state">
            <h4>
              <ResourceTextComponent resourceKey="search-empty-state" />
            </h4>
          </div>
        )}
      </div>
    </div>
  );
};

export { ProductsLineOverviewComponent };
