import * as React from "react";
import { NewsItem } from "@app/api";
import { WidgetItemsComponent, IWidgetItem } from "@app/modules/core/widget-items/widget-items.component";

export interface INewsSidebarWidgetComponentProps {
  newsItems: NewsItem[];
}
const convertNewsItems = (newsItems: NewsItem[]): IWidgetItem[] =>
  newsItems.map(item => ({
    imageUrl: item.image,
    imageLinkUrl: item.link,
    content: item.content
  }));

const NewsSidebarWidgetComponent = (props: INewsSidebarWidgetComponentProps) => (
  <WidgetItemsComponent
    header="Nieuws"
    readMoreLinkName="Meer nieuws"
    readMoreLinkUrl="/news-overview"
    widgetItems={convertNewsItems(props.newsItems)}
  />
);

export { NewsSidebarWidgetComponent };
