import * as React from "react";
import styles from "./cta-component.module.scss";
import { Button } from "@app/modules/core/button";
import { CTAModule } from "@app/api";

export interface ICtaComponentProps {
  ctaModule: CTAModule;
}

const CtaComponent = (props: ICtaComponentProps) => {
  const backgroundPic = props.ctaModule.imageURL
    ? {
        background: `linear-gradient(to right, #f5fafa, #f5fafa 59%, rgba(245, 250, 250, 0)) top left / 40% 100% no-repeat,  url(
      ${props.ctaModule.imageURL}
      ) 50% 50%/ cover no-repeat`
      }
    : undefined;

  return (
    <div style={{ backgroundColor: "#f5fafa" }} className={styles["cta-new"]}>
      <div className={styles["cta-new__background"]} style={backgroundPic} />
      <div
        className={`${styles[`cta-new__foreground`]} ${
          styles[`cta-new__foreground--${props.ctaModule.containerAlignment}`]
        } ${styles[`cta-new__foreground--content-${props.ctaModule.contentAlignment}`]}  `}
      >
        <div className={styles["cta-new__body"]}>
          <div className={styles["cta-new__title"]}>
            <h2>{props.ctaModule.titleText}</h2>
          </div>
          <div className={styles["cta-new__description"]}>{props.ctaModule.bodyText}</div>
          <ul
            className={`${styles[`cta-new__button-list`]} ${
              styles[`cta-new__button-list--content-${props.ctaModule.contentAlignment}`]
            } `}
          >
            {props.ctaModule.buttons &&
              props.ctaModule.buttons.map((button, index) => (
                <li key={index}>
                  <Button
                    href={button.href}
                    icon={button.icon}
                    target={button.target}
                    title={button.title}
                    buttonType={button.type}
                    variant={button.variant}
                  />
                </li>
              ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

export { CtaComponent };
