import { Dispatch } from "react-redux";
import { productLineActions } from "@app/stores/product-line";
import { ProductCardApi, ProductLineSingleModule, ProductOverviewSimpleModule } from "@app/api";
import FatalError from "@app/errors/fatal-error";

const getProductsWithPagination = (category: string, filter: string[], currentPage: number, productPerPage: number) => {
  const skip = currentPage * productPerPage - productPerPage;

  return async (dispatch: Dispatch<any>) => {
    try {
      const api = new ProductCardApi();

      api
        .productCardProductsPaginationGet(productPerPage, skip, filter, category)
        .then(response => response.json())
        .then((response: ProductOverviewSimpleModule) => {
          const totalPages = response.totalItems / productPerPage;

          dispatch(
            productLineActions.setProductOverviewSimple({
              productCardList: response.productCardList,
              totalItems: totalPages,
              productNumber: response.productCardList.length
            })
          );
        });
    } catch (error) {
      // new FatalError(error.name, error.message, error.stack);
    }
  };
};

const getCategoryList = (product: string[], category: string[]) => async (dispatch: Dispatch<any>) => {
  try {
    const api = new ProductCardApi();

    api
      .productCardCategoryGet(product, category)
      .then(response => response.json())
      .then((response: ProductLineSingleModule) => {
        dispatch(
          productLineActions.setCategoryCards({
            categoryCardList: response.categoryCardList,
            productCardList: response.productCardList,
            categoryNumber: response.productCardList.length,
            productNumber: response.productCardList.length
          })
        );
      });
  } catch (error) {
    // new FatalError(error.name, error.message, error.stack);
  }
};

const getProductList = (product: string[]) => async (dispatch: Dispatch<any>) => {
  try {
    const api = new ProductCardApi();

    api
      .productCardProductsGet(product)
      .then(response => response.json())
      .then((response: ProductLineSingleModule) => {
        dispatch(
          productLineActions.setProductCards({
            productCardList: response.productCardList,
            productNumber: response.productCardList.length
          })
        );
      });
  } catch (error) {
    // new FatalError(error.name, error.message, error.stack);
  }
};
const searchProductsCards = (filter: string) => async (dispatch: Dispatch<any>) => {
  try {
    const api = new ProductCardApi();

    api
      .productCardSearchGet(filter)
      .then(response => response.json())
      .then((response: ProductLineSingleModule) => {
        dispatch(
          productLineActions.setProductCards({
            productCardList: response.productCardList,
            productNumber: response.productCardList.length
          })
        );
      });
  } catch (error) {
    // new FatalError(error.name, error.message, error.stack);
  }
};

export const productLineThunks = {
  searchProductsCards,
  getProductsWithPagination,
  getProductList,
  getCategoryList
};
