import { connect, MapStateToProps, MapDispatchToProps } from "react-redux";
import { ItemOverviewComponent } from "@app/modules/item-overview";
import { IAppState, cardActions } from "@app/stores";
import { cardThunks } from "@app/middleware";
import { CardModel } from "@app/api";
import { withRouter } from "react-router";
import { IItemOverviewComponentProps } from "../item-overview.component";

interface IStateProps {
  cards: CardModel[];
  totalPages: number;
  currentPageNumber: number;
}
interface IDispatchProps {
  getCards: (take: number, page: number, filter?: string) => void;
  setTotalPage: (totalPages: number) => void;
  setCurrentPage: (totalPages: number) => void;
  setCards: (cards: CardModel[]) => void;
}

const mapStateToProps: MapStateToProps<IStateProps, IItemOverviewComponentProps, IAppState> = state => {
  const { cards, totalPages, currentPageNumber } = state.card;

  return { cards, totalPages, currentPageNumber };
};

const mapDispatchToProps: MapDispatchToProps<IDispatchProps, IItemOverviewComponentProps> = dispatch => ({
  getCards: (take: number, page: number, filter?: string) => dispatch(cardThunks.getCards(take, page, filter)),
  setCards: (cards: CardModel[]) => {
    dispatch(
      cardActions.setCards({
        cards: cards
      })
    );
  },
  setCurrentPage: (currentPageNumber: number) => {
    dispatch(
      cardActions.setCards({
        currentPageNumber
      })
    );
  },
  setTotalPage: (totalPages: number) => {
    dispatch(
      cardActions.setCards({
        totalPages
      })
    );
  }
});

export type ItemOverviewContainerProps = IStateProps & IDispatchProps;
export const ItemOverviewContainer = withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(ItemOverviewComponent)
);
