import React from "react";
import styles from "./product-line-cards-component.module.scss";
import { CategoryCard, ProductCard } from "@app/api";
import { ProductCardComponent } from "@app/modules/core/product-card";
import { CHEVRON_LEFT, CHEVRON_RIGHT } from "@app/constants/icons";
import Slider, { Settings } from "react-slick";
import { CategoryCardComponent } from "@app/modules/core/category-card";
export interface IProductLineCardsComponentProps {
  onSearch: boolean;
  quickFilterEmpty: boolean;
  productFilterEmpty: boolean;
  categoryCardList?: CategoryCard[];
  productCardList: ProductCard[];
  urlParams?: string;
}
const ProductLineCardsComponent = (props: IProductLineCardsComponentProps) => {
  const customPrevArrow = () => (
    <button>
      <img src={CHEVRON_LEFT} alt="arrow" />
    </button>
  );

  const customNextArrow = () => (
    <button>
      <img src={CHEVRON_RIGHT} alt="arrow" />
    </button>
  );
  const settings: Settings = {
    dots: false,
    infinite: false,
    nextArrow: customNextArrow(),
    prevArrow: customPrevArrow(),
    responsive: [
      {
        breakpoint: 959,
        settings: {
          slidesToScroll: 1,
          slidesToShow: 3
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToScroll: 1,
          slidesToShow: 2
        }
      },
      {
        breakpoint: 639,
        settings: "unslick"
      }
    ],
    slidesToScroll: 1,
    slidesToShow: 4,
    speed: 500
  };

  return (
    <React.Fragment>
      {props.categoryCardList && props.quickFilterEmpty ? (
        props.categoryCardList.map((category, index) => (
          <div key={index} className={styles[`product-overview__block`]}>
            <Slider ref={React.createRef()} {...settings} className="uk-visible@s">
              <CategoryCardComponent categoryCard={category} />
              {props.productCardList &&
                props.productCardList.map(
                  (subItem, count) =>
                    category.category.toUpperCase() === subItem.category.toUpperCase() && (
                      <ProductCardComponent key={count} productCard={subItem} />
                    )
                )}
            </Slider>
            <div className="uk-hidden@s">
              <CategoryCardComponent categoryCard={category} />
              {props.productCardList &&
                props.productCardList.map(
                  (subItem, count) =>
                    category.category.toUpperCase() === subItem.category.toUpperCase() && (
                      <ProductCardComponent key={count} productCard={subItem} />
                    )
                )}
            </div>
          </div>
        ))
      ) : (
        <div className="uk-grid uk-grid-small product-group__posts uk-child-width-1-2@s uk-child-width-1-3@m uk-child-width-1-4@l">
          {props.productCardList &&
            (!props.quickFilterEmpty || (!props.quickFilterEmpty && !props.productFilterEmpty) || props.onSearch) &&
            props.productCardList.map((subItem, count) => (
              <div key={count}>
                <ProductCardComponent key={count} productCard={subItem} />
              </div>
            ))}
        </div>
      )}
    </React.Fragment>
  );
};

export { ProductLineCardsComponent };
