import * as React from "react";

import { AuthorInfoComponent, LastColumnType, BorderPosition } from "../author-info";
import { BodyTextExpandedModule, BodyTextModule } from "@app/api";
import { BodyTextComponent } from "../body-text/body-text.component";

export interface INewsSingleItemComponentProps {
  bodyTextExpandedModule: BodyTextExpandedModule;
}

const NewsSingleItemComponent = (props: INewsSingleItemComponentProps) => {
  const bodyTextHeaderComponent = (
    <AuthorInfoComponent author={props.bodyTextExpandedModule.author} lastColumnType={LastColumnType.DATE} />
  );
  const bodyTextFooterComponent = (
    <AuthorInfoComponent
      author={props.bodyTextExpandedModule.author}
      lastColumnType={LastColumnType.SHARE}
      socialMediaButtons={props.bodyTextExpandedModule.socialMediaButtons}
      borderPosition={BorderPosition.TOP}
    />
  );

  return (
    <BodyTextComponent
      bodyTextModule={props.bodyTextExpandedModule as BodyTextModule}
      header={bodyTextHeaderComponent}
      footer={bodyTextFooterComponent}
    />
  );
};

export { NewsSingleItemComponent };
