import * as React from "react";

import style from "./subscribe-component.module.scss";
import { Link } from "react-router-dom";
import { Button } from "@app/modules/core/button";
import { validateEmail } from "@app/util/validate-email";
import ArrowRight from "@assets/icons/arrow-right.svg";

interface ISubscribeProps {}

const Subscribe = (props: ISubscribeProps) => {
  const [name, setName] = React.useState<string>("");
  const [email, setEmail] = React.useState<string>("");
  const onSubmitForm = (event: React.FormEvent<EventTarget>) => {
    event.preventDefault();
    if (name.trim() && email.trim()) {
      if (validateEmail(email)) {
        alert(`You are now registered in our newsletter!`);
      } else {
        alert("Please enter a valid email address");
      }
    } else {
      alert("Please enter your name and email");
    }
  };
  const onNameChange = (event: React.FormEvent<EventTarget>) => {
    const target = event.target as HTMLInputElement;
    setName(target.value);
  };

  const onEmailChange = (event: React.FormEvent<EventTarget>) => {
    const target = event.target as HTMLInputElement;
    setEmail(target.value);
  };

  return (
    <form onSubmit={onSubmitForm} method="POST" className={style["form"]}>
      <div className={style["form__row"]}>
        <div className={style["form__area"]}>
          <label htmlFor="">
            <input
              type="text"
              value={name}
              name="name"
              placeholder="Naam"
              className={style["form__input"]}
              onChange={onNameChange}
            />
          </label>
        </div>
        <div className={style["form__area"]}>
          <label htmlFor="email">
            <input
              type="email"
              name="email"
              placeholder="E-mailadres"
              value={email}
              className={style["form__input"]}
              onChange={onEmailChange}
            />
          </label>
        </div>
      </div>
      <div className={style["form__bottom"]}>
        <div className={style["form__text"]}>
          Dit móeten we mededelen: wanneer je op verzenden klikt, ga je akkoord met ons{" "}
          <Link to="/" target="_blank">
            privacy policy.
          </Link>
        </div>
        <Button icon={ArrowRight} title="Verzenden" variant="primary" />
      </div>
    </form>
  );
};

export { Subscribe };
