import { FilterOptions } from "@app/api";

/**
 *
 * @param filterOptions this function checks if any filter option is checked . it returns true if there is no option checked and false if there is at least one option checked
 */
function isQuickFilterEmpty(filterOptions: FilterOptions[]) {
  let i = 0;
  filterOptions.forEach(options => {
    if (options.value) {
      i += 1;
    }
  });
  if (i === 0) {
    return true;
  }

  return false;
}

export { isQuickFilterEmpty };
