import * as React from "react";
import styles from "./card-component.module.scss";
import { Link } from "react-router-dom";
import { OurTeamCards } from "@app/api";

export interface ICardComponentProps {
  cards: OurTeamCards;
}

const CardComponent = (props: ICardComponentProps) => (
  <div className={styles[`outside`]}>
    <div className={styles[`container`]}>
      <div className={styles[`card`]}>
        <div className={styles[`card__user`]}>
          {props.cards.image && (
            <div className={styles[`card__media`]}>
              <div className={styles[`card__image`]}>
                <img src={props.cards.image} alt={`${props.cards.title}'s card`} />
              </div>
            </div>
          )}
          <div className={styles[`card__body`]}>
            <div className={styles[`card__head`]}>
              <div className={styles[`card__title`]}>
                <h4>{props.cards.title}</h4>
              </div>
              {props.cards.social && (
                <Link to={props.cards.link}>
                  <span className={styles[`card__social`]} />
                </Link>
              )}

              <div className={styles[`card__position`]}>{props.cards.position}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export { CardComponent };
