import * as React from "react";
import styles from "./widget-items-component.module.scss";
import { Link } from "react-router-dom";
import { Button } from "@app/modules/core";

export interface IWidgetItem {
  imageUrl: string;
  imageLinkUrl: string;
  content: string;
}

export interface IWidgetItemsComponentProps {
  header: string;
  widgetItems: IWidgetItem[];
  readMoreLinkName: string;
  readMoreLinkUrl: string;
  max?: number;
}

const WidgetItemsComponent = (props: IWidgetItemsComponentProps) => (
  <div className={styles[`widget-items`]}>
    <div className={styles[`widget-items__title`]}>
      <h4>{props.header}</h4>
    </div>
    <ul className={styles[`widget-items__list`]}>
      {props.widgetItems.slice(0, props.max).map((item: IWidgetItem) => (
        <li>
          <Link to={"/"} className={styles[`widget-items__card`]}>
            <span className={styles[`widget-items__card-image`]}>
              <img src={item.imageUrl} alt="alt" />
            </span>
            <span className={styles[`widget-items__card-title`]}>{item.content}</span>
          </Link>
        </li>
      ))}
    </ul>

    <div className={styles[`widget-items__bottom`]}>
      <Button title={props.readMoreLinkName} href={props.readMoreLinkName} variant={"default"} />
    </div>
  </div>
);

export { WidgetItemsComponent };
