import React from "react";
import { FilterOptions, Recipe, NewsItem } from "@app/api";
import { FilterComponent } from "@app/modules/core/filter";
import { NewsSidebarWidgetComponent } from "./news-sidebar-widget.component";
import { RecipesSidebarWidgetsComponent } from "./recipes-sidebar-widget.component";
import styles from "./product-overview-sidebar-component.module.scss";
import { SearchForm, IconComponent, ResourceTextComponent } from "@app/modules/core";
import FilterIcon from "@assets/icons/filter.svg";
export interface ISidebarComponentProps {
  quickFilterOptions: FilterOptions[];
  onQuickFilterChanged: (value: string) => FilterOptions[];
  productFilterOptions: FilterOptions[];
  onProductFilterChanged: (value: string) => FilterOptions[];
  relatedNewsItems: NewsItem[];
  relatedRecipeItems: Recipe[];
  onSearchFormChanged: (product: string) => void;
  quickFilterTitle: string;
  productFilterTitle: string;
  showMoreButton: string;
  productFilterMax: number;
  quickFilterMax: number;
}

const SidebarComponent = (props: ISidebarComponentProps) => {
  const [isFilterClicked, setIsFilterClicked] = React.useState(true);

  return (
    <React.Fragment>
      <div className={`${styles[`sidebar`]} `}>
        <div className={styles[`sidebar__search`]}>
          <SearchForm placeholder="Zoek je product" onChange={props.onSearchFormChanged} />
        </div>

        <button
          onClick={() => {
            setIsFilterClicked(!isFilterClicked);
          }}
          className={`${styles[`uk-accordion-title`]}  ${`uk-hidden@s`}`}
        >
          <span>
            <ResourceTextComponent resourceKey="filter" />
          </span>
          <IconComponent icon={FilterIcon} size="16px" />
        </button>
        <div className={`${styles[`uk-accordion-content`]} ${styles[isFilterClicked ? "responsive" : ""]}`}>
          <div className={styles[`sidebar__block`]}>
            <FilterComponent
              filterOptions={props.quickFilterOptions}
              onFilterChanged={props.onQuickFilterChanged}
              showMoreTitle={props.showMoreButton}
              title={props.quickFilterTitle}
              maxDisplay={props.quickFilterMax}
            />
          </div>
          {props.productFilterOptions.length > 1 && (
            <div className={styles[`sidebar__block`]}>
              <FilterComponent
                filterOptions={props.productFilterOptions}
                onFilterChanged={props.onProductFilterChanged}
                showMoreTitle={props.showMoreButton}
                title={props.productFilterTitle}
                maxDisplay={props.productFilterMax}
              />
            </div>
          )}

          <div className={styles[`sidebar__block`]}>
            <NewsSidebarWidgetComponent newsItems={props.relatedNewsItems} />
          </div>

          <div className={styles[`sidebar__block`]}>
            <RecipesSidebarWidgetsComponent recipes={props.relatedRecipeItems} />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};
export { SidebarComponent };
