import * as React from "react";
import { Link } from "react-router-dom";
import "./item-card.component.scss";
import { Image } from "@app/api";

interface IItemCardProps {
  link: string;
  title?: string;
  image?: Image;
  labels: JSX.Element[];
  category?: string;
  button?: JSX.Element;
}

const ItemCard = (props: IItemCardProps) => (
  <div>
    <div className="item-card item-card--post">
      <div className="item-card__shadow" />
      <div className="item-card__holder">
        {props.image && (
          <div className="item-card__media">
            <div className="item-card__image">
              <Link to={props.link}>
                <img src={props.image.url} alt={props.image.alt} />
              </Link>
            </div>
          </div>
        )}
        <div className="item-card__body">
          <div className="item-card__head">
            <div className="item-card__info">
              <div className="item-card__category">
                <span className="item-card__category-item">{props.category}</span>
              </div>
              {props.labels.map((item, key) => (
                <div key={key} className="item-card__info-date">
                  {item}
                </div>
              ))}
            </div>
            <div className="item-card__title">{props.title}</div>
            <div className="item-card__bottom">{props.button}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export { ItemCard };
