import * as React from "react";
import { Link } from "react-router-dom";
import { IconComponent } from "@app/modules/core";
import ArrowLeft from "@assets/icons/arrow-left.svg";
import styles from "./info-header-component.module.scss";
import { InfoHeaderModule } from "@app/api";

import ArrowRight from "@assets/icons/arrow-right.svg";
import { Button } from "../core/button";
import { HtmlRenderComponent } from "../core/html-render";

export interface IInfoHeaderComponentProps {
  infoHeaderModule: InfoHeaderModule;
}

const InfoHeaderComponent = (props: IInfoHeaderComponentProps) => {
  const marginByProps = {
    marginBottom: props.infoHeaderModule.bottomMargin || "",
    marginTop: props.infoHeaderModule.topMargin || ""
  };

  return (
    <div
      style={marginByProps}
      className={`${styles["info-header"]} ${styles["info-header--" + `${props.infoHeaderModule.align || "left"}`]}`}
    >
      <div className={`uk-container ${styles["info-header__container"]}`}>
        <div
          className={`${styles["info-header__foreground"]} ${
            styles["info-header__foreground--" + `${props.infoHeaderModule.align || "right"}`]
          }`}
        >
          <div className={styles["info-header__body"]}>
            {props.infoHeaderModule.backButtonText && (
              <Link to={props.infoHeaderModule.backButtonURL} className={styles["info-header__back"]}>
                <IconComponent icon={ArrowLeft} size="12px" />
                {props.infoHeaderModule.backButtonText}
              </Link>
            )}

            <h1>{props.infoHeaderModule.titleText}</h1>
            <p className={styles["info-header__body-text"]}>{props.infoHeaderModule.bodyText}</p>
            {props.infoHeaderModule.htmlArea && <HtmlRenderComponent html={props.infoHeaderModule.htmlArea} />}
            {props.infoHeaderModule.buttonLinkText && (
              <div className={styles["info-header__button"]}>
                <Button
                  icon={ArrowRight}
                  href={props.infoHeaderModule.buttonLinkURL}
                  variant="default"
                  title={props.infoHeaderModule.buttonLinkText}
                />
              </div>
            )}
          </div>
          {props.infoHeaderModule.imageURL && (
            <div className={styles["info-header__image"]}>
              <img src={props.infoHeaderModule.imageURL} alt="image" />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export { InfoHeaderComponent };
