import { ProductCard, ProductLineSingleModule, CategoryCard } from "@app/api";
import { MapStateToProps, MapDispatchToProps, connect } from "react-redux";
import { productLineThunks } from "@app/middleware/thunk/product-line.thunk";
import { productLineActions } from "@app/stores/product-line";
import { IAppState } from "@app/stores";
import { IProductLineFilterComponentProps, ProductLineFilterComponent } from "../components";
import { withRouter } from "react-router";

interface IStateProps {
  productCardList: ProductCard[];
  categoryCardList: CategoryCard[];
  productNumber: number;
  categoryNumber: number;
}

interface IDispatchProps {
  searchProductsCards: (filter: string) => void;
  setProductLineModule: (productLine: ProductLineSingleModule) => void;
  getCategoryCards: (category: string[], product: string[]) => void;
  getProductCards: (product: string[]) => void;
}

const mapStateToProps: MapStateToProps<IStateProps, IProductLineFilterComponentProps, IAppState> = state => {
  const { productCardList, categoryCardList, productNumber, categoryNumber } = state.categoryList;

  return { productCardList, categoryCardList, productNumber, categoryNumber };
};

const mapDispatchToProps: MapDispatchToProps<IDispatchProps, IProductLineFilterComponentProps> = dispatch => ({
  searchProductsCards: (filter: string) => dispatch(productLineThunks.searchProductsCards(filter)),
  getCategoryCards: (category: string[], product: string[]) =>
    dispatch(productLineThunks.getCategoryList(category, product)),
  getProductCards: (product: string[]) => dispatch(productLineThunks.getProductList(product)),

  setProductLineModule: (productLine: ProductLineSingleModule) =>
    dispatch(productLineActions.setProductLine(productLine))
});

export type ProductOverviewContainerProps = IStateProps & IDispatchProps;

export const ProductOverviewContainer = withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(ProductLineFilterComponent)
);
