import * as React from "react";

import styles from "./hamburger.button-component.module.scss";
export interface IHamburgerButtonComponentProps {
  isOpen: boolean;
  onOpenChanged: () => void;
  text?: string;
}

const HamburgerButtonComponent = (props: IHamburgerButtonComponentProps) => (
  <div className={styles[`container`]} role="button" onClick={props.onOpenChanged}>
    {props.text && <p className={styles[`${props.isOpen ? "opened" : ""}`]}>{props.text}</p>}
    <div className={styles["hamburger-container"]}>
      <div className={` ${styles[`hamburger-menu`]} ${styles[props.isOpen ? "opened" : ""]}`}>
        <span />
      </div>
    </div>
  </div>
);

export { HamburgerButtonComponent };
