import * as React from "react";
import styles from "./get-modules-from-page.module.scss";
import { NavBarComponent } from "@app/modules/nav-bar";
import {
  WordPressPostModule,
  NavBarModule,
  CTAModule,
  InfoHeaderModule,
  WelcomeModule,
  FooterModule,
  NotYetImplemented,
  InstagramWalllModule,
  BodyTextExpandedModule,
  OurTeamModule,
  ContactModule,
  SalePointModule,
  Background,
  NewsletterModule,
  RecipeDetailModule,
  RelatedLinksModule,
  ItemOverviewModule,
  FAQOverviewModule,
  RelatedProductsSlider,
  ProductOverviewSimpleModule,
  ProductLineSingleModule,
  ProductLineOverviewModule
} from "@app/api";
import { CtaComponent } from "@app/modules/cta";
import { InfoHeaderComponent } from "@app/modules/info-header";
import { BodyTextComponent } from "@app/modules/body-text";
import { WelcomeComponent, InstagramWallComponent } from "@app/modules";
import { FooterComponent, generateFilledFSocialMediaButtons } from "@app/modules/footer";
import { NotYetImplementedComponent } from "@app/modules/core/not-yet-implemented";
import { NewsSingleItemComponent } from "@app/modules/news-single-item";
import { OurTeamComponent } from "@app/modules/our-team";
import { ContactComponent } from "@app/modules/contact";
import { RelatedProductsSliderComponent } from "@app/modules/related-products-slider";
import { SalePointComponent } from "@app/modules/sale-point";
import { ProductOverviewContainer } from "@app/modules/product-line-filter/container";
import { ProductOverviewSimpleContainer } from "@app/modules/product-overview-simple/container";
import { FaqOverviewComponent } from "@app/modules/faq-overview";
// import { PlaygroundContainer } from "@app/pages/playground";
import { NewsletterComponent } from "@app/modules/newsletter";
import { RecipeDetail } from "@app/modules/recipe-detail";
import { RelatedLinksComponent } from "@app/modules/related-links/related-links.component";
import { ProductsLineOverviewComponent } from "@app/modules/product-line-overview/components";
import { ItemOverviewContainer } from "@app/modules/item-overview/containers";

export const getModule = (wordPressModule: WordPressPostModule) => {
  const component = getComponent(wordPressModule);

  const styleMainContainerModule = {
    ...getBackgroundStyleProperties(wordPressModule.background as Background),
    paddingTop: wordPressModule.topPadding,
    paddingBottom: wordPressModule.bottomPadding
  };

  return (
    <div className={styles["contentComponent"]} style={styleMainContainerModule}>
      {component}
    </div>
  );
};

const getComponent = (wordPressModule: WordPressPostModule) => {
  switch (wordPressModule.name) {
    case "Navbar":
      return <NavBarComponent navBarModule={wordPressModule as NavBarModule} />;
    case "WelcomeModule":
      return <WelcomeComponent bgcolor="white" welcomeModule={wordPressModule as WelcomeModule} />;
    case "CTAModule":
      return <CtaComponent ctaModule={wordPressModule as CTAModule} />;
    case "infoHeaderModule":
      return <InfoHeaderComponent infoHeaderModule={wordPressModule as InfoHeaderModule} />;
    case "bodyTextModule":
      return <BodyTextComponent bodyTextModule={wordPressModule as BodyTextExpandedModule} />;
    case "FooterModule":
      return (
        <FooterComponent
          socialMediaButtons={generateFilledFSocialMediaButtons()}
          footerModule={wordPressModule as FooterModule}
        />
      );
    case "InstagramModule":
      return <InstagramWallComponent instagramWallModule={wordPressModule as InstagramWalllModule} />;
    case "ProductLineStoryOverviewModule":
      return <ProductsLineOverviewComponent productLineOverviewModule={wordPressModule as ProductLineOverviewModule} />;
    case "bodyTextExpandedModule":
      return <NewsSingleItemComponent bodyTextExpandedModule={wordPressModule as BodyTextExpandedModule} />;
    case "OurTeamComponent":
      return <OurTeamComponent ourTeam={wordPressModule as OurTeamModule} />;
    case "ContactComponent":
      return <ContactComponent contact={wordPressModule as ContactModule} />;
    case "RelatedLinksModule":
      return <RelatedLinksComponent relatedLinks={wordPressModule as RelatedLinksModule} />;
    case "SalePointComponent":
      return <SalePointComponent salePoint={wordPressModule as SalePointModule} />;
    case "ProductLineSingleModule":
      return <ProductOverviewContainer productLine={wordPressModule as ProductLineSingleModule} />;
    case "ProductOverviewSimpleModule":
      return <ProductOverviewSimpleContainer productOverview={wordPressModule as ProductOverviewSimpleModule} />;
    case "FAQOverviewModule":
      return <FaqOverviewComponent faqOverviewModule={wordPressModule as FAQOverviewModule} />;
    case "RecipeOverviewModule":
      return <ItemOverviewContainer itemOverviewModule={wordPressModule as ItemOverviewModule} />;
    // case "PlaygroundModule":
    //   return <PlaygroundContainer />;
    case "NewsletterComponent":
      return <NewsletterComponent newsletterModule={wordPressModule as NewsletterModule} />;
    case "RecipeOverviewModule":
      return <ItemOverviewContainer itemOverviewModule={wordPressModule as ItemOverviewModule} />;
    case "RelatedProductsSlider":
      return <RelatedProductsSliderComponent relatedProductsModule={wordPressModule as RelatedProductsSlider} />;
    case "notYetImplemented":
      return <NotYetImplementedComponent notYetImplemented={wordPressModule as NotYetImplemented} />;
    default:
  }
};

const getBackgroundStyleProperties = (backgroundModule: Background) => {
  let backgroundProperty = null;

  if (backgroundModule) {
    if (backgroundModule.backgroundPattern) {
      backgroundProperty = {
        background: `url(${backgroundModule.backgroundPattern})`,
        backgroundPosition: "50% 50%",
        backgroundRepeat: `repeat`
      };
    } else {
      backgroundProperty = {
        background: backgroundModule.backgroundColour
      };
    }
  } else {
    backgroundProperty = {
      background: "#fff"
    };
  }

  return backgroundProperty;
};
