/// <reference path="./../../../types/index.d.ts" />

import thunk from "redux-thunk";
import { createStore, applyMiddleware, compose } from "redux";
import { routerMiddleware, RouterState } from "connected-react-router";
import { History } from "history";
import { combineReducers } from "redux";
import { connectRouter } from "connected-react-router";
import { localesReducer } from "@app/stores/locales";

import { ILocalesState } from "@app/stores/locales";
import { ICardState, cardReducer } from "./card";
import {
  ICategoryCards,
  IProductLine,
  IProductCards,
  IProductOverviewSimples,
  productOverviewSimpleReducer
} from "./product-line";
import { IPageState, pageReducer } from "./page";

type StoreParams = {
  history: History;
  initialState: IAppState;
  middleware?: any[];
};

export interface IAppState {
  card: ICardState;
  categoryList: ICategoryCards;
  locales: ILocalesState;
  page: IPageState;
  productLine: IProductLine;
  productList: IProductCards;
  productOverviewSimple: IProductOverviewSimples;
  router: RouterState;
}

export const getInitialState = () => {
  const initialState = <IAppState>{};
  return initialState;
};

declare let window: ExtendedWindow;
export const configureStore = ({ history, initialState, middleware = [] }: StoreParams) => {
  const devtools =
    process.env.NODE_ENV === "development" &&
    typeof window !== "undefined" &&
    typeof window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ === "function" &&
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({ actionsBlacklist: [] });

  const composeEnhancers = devtools || compose;

  const store = createStore<IAppState>(
    combineReducers({
      locales: localesReducer,
      card: cardReducer,
      page: pageReducer,
      productOverviewSimple: productOverviewSimpleReducer,
      productLine: productOverviewSimpleReducer,
      categoryList: productOverviewSimpleReducer,
      router: connectRouter(history)
    }),
    initialState,
    composeEnhancers(applyMiddleware(...[thunk, routerMiddleware(history)].concat(...middleware)))
  );

  // if (process.env.NODE_ENV !== "production") {
  //   if (module.hot) {
  //     module.hot.accept("./rootReducer", () => store.replaceReducer(require("./rootReducer").default));
  //   }
  // }

  return store;
};

export default configureStore;
