import * as React from "react";
import styles from "./decor-curve-component.module.scss";
import { Curve, Background } from "@app/api";
interface IDecorCurveProps {
  decorCurveModule: Curve;
}
const getCurvePosition = (curveVerticalAlignment?: string, curveHorizontalAlignment?: string): string => {
  const verticalAlignment = curveVerticalAlignment ? `decor-curve--${curveVerticalAlignment}` : "decor-curve--top"; // default value
  const horizontalAlignment = curveHorizontalAlignment
    ? `${verticalAlignment}--${curveHorizontalAlignment}`
    : `${verticalAlignment}--left`; // default value

  return `${styles[verticalAlignment]} ${styles[horizontalAlignment]}`;
};

const getCurveBackground = (curveBackground: Background | undefined) => {
  let fillCurve = null;
  fillCurve = curveBackground
    ? curveBackground.backgroundPattern
      ? "url(#pattern)"
      : curveBackground.backgroundColour
    : "#fff";

  return fillCurve;
};

const DecorCurveComponent = (props: IDecorCurveProps) => {
  const backgroundContent = getCurveBackground(props.decorCurveModule.curveBackground);

  return (
    <React.Fragment>
      {props.decorCurveModule.decorCurveVerticalAlignment === "middle" && (
        <div
          className={styles["topCurve"]}
          style={{
            background:
              backgroundContent === "url(#pattern)"
                ? `url(${(props.decorCurveModule.curveBackground as Background).backgroundPattern})`
                : backgroundContent
          }}
        />
      )}
      <div
        className={`${styles["decor-curve"]} ${getCurvePosition(
          props.decorCurveModule.decorCurveVerticalAlignment,
          props.decorCurveModule.decorCurveHorizontalAlignment
        )}`}
      >
        <svg xmlns="www.w3.org/2000/svg" viewBox="0 0 1920 187">
          <g>
            <path
              d="M0,145.908V187H1920V0C1271.53,189.747,623.826,228.572,0,145.908Z"
              fillRule="evenodd"
              fill={backgroundContent}
            />
          </g>
          <defs>
            <pattern x="0" y="0" patternUnits="userSpaceOnUse" width="500" height="499" id="pattern">
              <image
                href={
                  props.decorCurveModule.curveBackground
                    ? props.decorCurveModule.curveBackground.backgroundPattern
                      ? props.decorCurveModule.curveBackground.backgroundPattern
                      : ""
                    : ""
                }
                width="700"
                height="700"
              />
            </pattern>
          </defs>
        </svg>
      </div>
    </React.Fragment>
  );
};

export { DecorCurveComponent };
