import * as React from "react";

import styles from "./social-media-component.module.scss";
import { SocialMediaButtons } from "@app/api";
import { Link } from "react-router-dom";
import { IconComponent, ResourceTextComponent } from "@app/modules/core";

export enum SocialMediaComponentType {
  LINK,
  SHARE
}

export interface ISocialMediaComponentProps {
  socialMediaButtons: SocialMediaButtons;
  socialMediaComponentType: SocialMediaComponentType;
}

const SocialMediaComponent = (props: ISocialMediaComponentProps) => (
  <div className={styles[`social-media__social`]}>
    <ul className={styles[`social-media__nav`]}>
      <li className={`uk-visible@m`}>
        <span>
          <ResourceTextComponent
            resourceKey={
              props.socialMediaComponentType === SocialMediaComponentType.LINK
                ? "social-media-follow-us"
                : "social-media-share"
            }
          />
        </span>
      </li>
      {props.socialMediaButtons.footerButtons.map((button, index) => (
        <li key={index}>
          <Link to="https://www.facebook.com/sharer/sharer.php?u=example.org">
            <IconComponent icon={button.icon} size="24px" color="$brand-primary" />
          </Link>
        </li>
      ))}
    </ul>
  </div>
);

export { SocialMediaComponent };
