import * as React from "react";
import styles from "./footer-component.module.scss";
import { Link } from "react-router-dom";
import { FooterModule } from "@app/api";

export interface ITopFooterComponentProps {
  footerModule: FooterModule;
}
const TopFooterComponent = (props: ITopFooterComponentProps) => (
  <div className={styles[`footer__top`]}>
    {props.footerModule.footerSections.map((element, index) => (
      <div className={styles[`footer__section`]} key={index}>
        <div className={styles[`footer__section-title`]}>
          <span>{element.title}</span>
        </div>
        <ul className={styles[`footer__section-list`]}>
          {element.links.map((list, elementIndex) => (
            <li key={elementIndex}>
              <Link to={list.route}>
                <span>{list.content}</span>
              </Link>
            </li>
          ))}
        </ul>
      </div>
    ))}
  </div>
);
export { TopFooterComponent };
