import * as React from "react";
import styles from "./related-products-slider-component.module.scss";
import Slider, { Settings } from "react-slick";
import { RelatedProductsSlider } from "@app/api";
import { Button } from "@app/modules/core/button";
import ArrowRight from "@assets/icons/arrow-right.svg";
import { RelatedPostCard } from "../core/related-post-card";
import { setRelatedProductsSlider } from "./slider-settings";

export interface IRelatedProductsProps {
  relatedProductsModule: RelatedProductsSlider;
  customNextArrow?: JSX.Element | undefined;
  customPrevArrow?: JSX.Element | undefined;
}

const numItemsMobile = 4;

const RelatedProductsSliderComponent = (props: IRelatedProductsProps) => {
  const [draggable, setDraggable] = React.useState(false);
  const settingsSlider = setRelatedProductsSlider(
    () => setDraggable,
    props.customNextArrow,
    props.customPrevArrow
  ) as Settings;

  return (
    <div className={styles["related-product-slider"]}>
      <div className={styles["related-product-slider__foreground"]}>
        <div className="uk-container">
          <div className={styles["related-product-slider__header"]}>
            <h2>{props.relatedProductsModule.title}</h2>
            <div className={styles["related-product-slider__header-button"]}>
              <Button title={props.relatedProductsModule.buttonText} variant="primary" icon={ArrowRight} />
            </div>
          </div>
          <Slider {...settingsSlider}>
            {props.relatedProductsModule.relatedProducts.map((item, index) => (
              <div className={styles["related-product-slider__slide"]} key={index}>
                <RelatedPostCard link={item.URL} title={item.title} image={item.picture} draggable={draggable} />
              </div>
            ))}
          </Slider>
          <div className={styles["related-product-slider__mobile-container"]}>
            {props.relatedProductsModule.relatedProducts.slice(0, numItemsMobile).map((item, index) => (
              <div className={styles["related-product-slider__slide"]} key={index}>
                <RelatedPostCard link={item.URL} title={item.title} image={item.picture} draggable={draggable} />
              </div>
            ))}
          </div>
          <div className={`${styles["related-product-slider__bottom-button"]}`}>
            <Button title={props.relatedProductsModule.buttonText} variant="primary" icon={ArrowRight} />
          </div>
        </div>
      </div>
    </div>
  );
};

export { RelatedProductsSliderComponent };
