import * as React from "react";
import style from "./button-component.module.scss";
import { Link } from "react-router-dom";
import { IconComponent } from "@app/modules/core";
import classNames from "classnames";
interface IButtonProps {
  title?: string;
  variant?: string;
  href?: string;
  target?: string;
  buttonType?: string;
  classNames?: void;
  fullWidth?: boolean;
  icon?: string;
  onClick?: () => void;
}

const Button = (props: IButtonProps) => {
  const { href, variant, target, title, buttonType, icon } = props;
  const classModify = variant || "default";
  const buttonFAB = !title ? style["button--FAB"] : "";
  const buttonClassName = classNames(style["button"], style[`button--${classModify}`], buttonFAB, {
    "button--fullWidth": props.fullWidth
  });

  return (
    <React.Fragment>
      {href ? (
        <Link to={href} className={buttonClassName} target={target}>
          {title}
          {icon && <IconComponent icon={icon} size="14px" />}
        </Link>
      ) : (
        <button type={buttonType} className={buttonClassName} onClick={props.onClick}>
          {props.title}
          {icon && <IconComponent icon={icon} size="14px" />}
        </button>
      )}
    </React.Fragment>
  );
};

export { Button };
