import * as React from "react";
import styles from "./navbar-search-component.module.scss";
import { NavbarSearchCategory } from "@app/api";
import { navbarSearchThunks } from "@app/middleware/thunk/navbar-search.thunk";
import Highlighter from "react-highlight-words";
import { ResourceTextComponent } from "@app/modules/core";
import { SEARCH_ICON, CLOSE_ICON } from "@app/constants/icons";

export interface INavbarSearchComponentProps {
  opened: boolean;
  onClose: () => void;
}
const NavbarSearchComponent = (props: INavbarSearchComponentProps) => {
  const { opened } = props;
  const [items, setItems] = React.useState<NavbarSearchCategory[]>([]);
  const [filter, setFilter] = React.useState<string>("");
  const [shouldShowEmptyState, setShouldShowEmptyState] = React.useState<boolean>(false);
  const searchField = React.useRef<HTMLInputElement>(null);

  const searchItems = (keyword: string) => {
    const trimmedFilter = keyword.trim();
    setFilter(trimmedFilter);

    if (trimmedFilter) {
      if (!shouldShowEmptyState) {
        setShouldShowEmptyState(true);
      }
      navbarSearchThunks
        .searchItems(trimmedFilter)
        .then(response => response.json())
        .then(setItems);
    } else {
      setShouldShowEmptyState(false);
      setItems([]);
    }
  };

  React.useEffect(() => {
    setItems([]);
    setFilter("");
    setShouldShowEmptyState(false);
    if (searchField.current) {
      searchField.current.focus();
    }
  }, [props.opened]);

  const styleOpened = opened ? "opened" : "closed";

  return (
    <div className={`${styles["navbar-search"]} ${styles[styleOpened]} `}>
      <div role="div" onClick={props.onClose} className={`${styles["navbar-search__shadow"]} ${styles[styleOpened]}`} />
      <div className={`${styles["navbar-search__bar"]} ${styles[styleOpened]}`}>
        <div className="uk-container">
          <div className={styles["navbar-search__bar__top"]}>
            <div role="div" onClick={props.onClose} className={styles["navbar-search__bar__top__item__close"]}>
              <img src={CLOSE_ICON} alt="close icon" />
            </div>
            <input
              ref={searchField}
              value={filter}
              onChange={input => {
                searchItems(input.currentTarget.value);
              }}
              placeholder="Search in the Website"
              className={styles["navbar-search__bar__top__item__input"]}
            />
            <img src={SEARCH_ICON} alt="Image" />
          </div>
        </div>
        <div className={styles["navbar-search__results"]}>
          {items.length > 0 ? (
            items.map((category: NavbarSearchCategory, key) => (
              <div className={styles["navbar-search__results__item"]} key={key}>
                <h4>{category.name}</h4>
                {category.items.map((item, keyx) => (
                  <div key={keyx} className={styles["navbar-search__results__item__details"]}>
                    <a>
                      <Highlighter
                        highlightClassName={styles["highlighted"]}
                        searchWords={[filter]}
                        autoEscape={true}
                        textToHighlight={item.name}
                      />
                    </a>
                  </div>
                ))}
              </div>
            ))
          ) : (
            <h3>{shouldShowEmptyState && <ResourceTextComponent resourceKey="search-empty-state" />}</h3>
          )}
        </div>
      </div>
    </div>
  );
};

export { NavbarSearchComponent };
