import * as React from "react";

import styles from "./author-info-component.module.scss";
import { Author, SocialMediaButtons } from "@app/api";
import { SocialMediaComponent, SocialMediaComponentType } from "../social-media";
import { Link } from "react-router-dom";

export enum LastColumnType {
  DATE,
  SHARE
}

export enum BorderPosition {
  TOP,
  BOTTOM
}

export interface IAuthorInfoComponentProps {
  author: Author;
  lastColumnType: LastColumnType;
  socialMediaButtons?: SocialMediaButtons;
  borderPosition?: BorderPosition;
}

const AuthorInfoComponent = (props: IAuthorInfoComponentProps) => (
  <div
    className={`${styles["author-info__post-info"]}
      ${props.borderPosition === BorderPosition.TOP && styles["author-info__top-border"]}
      ${props.borderPosition === BorderPosition.BOTTOM && styles["author-info__bottom-border"]}
      `}
  >
    <div className={styles["author-info__post-info-author"]}>
      <div className={styles["author-info__post-info-avatar"]}>
        <Link to={props.author.profileURL}>
          <img src={props.author.iconURL} alt="img" />
        </Link>
      </div>
      <div>
        <Link to={props.author.profileURL}>
          <div className={styles["poster-info__post-info-name"]}>{props.author.displayName}</div>
        </Link>
        <div>{props.author.title}</div>
      </div>
    </div>
    {props.lastColumnType === LastColumnType.SHARE && !!props.socialMediaButtons ? (
      <SocialMediaComponent
        socialMediaButtons={props.socialMediaButtons}
        socialMediaComponentType={SocialMediaComponentType.SHARE}
      />
    ) : (
      <div className={styles["poster-info__post-info-date"]}>{props.author.date}</div>
    )}
  </div>
);

export { AuthorInfoComponent };
