import * as React from "react";
import { LanguageSwitchContainer } from "@app/modules/core";
import styles from "./nav-bar-component.module.scss";
import { NavBarModule } from "@app/api";
import { Link } from "react-router-dom";
import { HamburgerButtonComponent } from "../core/hamburger-button";
import { NavbarSearchComponent } from "../navbar-search/navbar-search.component";
import { SEARCH_ICON } from "@app/constants/icons";

export interface INavBarProps {
  navBarModule: NavBarModule;
}

const NavBarComponent = (props: INavBarProps) => {
  const [isMenuOpened, setIsMenuOpened] = React.useState(false);
  const [isSearching, setIsSearching] = React.useState(false);
  const stopSearching = () => {
    setIsSearching(false);
  };
  const toggleOpened = () => {
    setIsMenuOpened(!isMenuOpened);
  };

  return (
    <div className={styles["nav-bar"]}>
      <div className={styles[`icon`]}>
        <div
          role="button"
          onClick={() => {
            setIsSearching(true);
            setIsMenuOpened(false);
          }}
          className={styles["nav-bar__bottom__search"]}
        >
          <img src={SEARCH_ICON} alt="Image" />
        </div>
        <HamburgerButtonComponent isOpen={isMenuOpened} onOpenChanged={toggleOpened} text="Menu" />
      </div>
      <nav className={`${styles[`nav-bar__top`]} ${styles[isMenuOpened ? "responsive" : ""]}`}>
        <div className="uk-container">
          <ul className={`${styles[`nav-bar__top-menu`]} ${styles[isMenuOpened ? "responsive" : ""]}`}>
            {props.navBarModule.topLinks.map((link, index) => (
              <li key={index}>
                <Link to={link.route}>{link.content}</Link>
              </li>
            ))}
            <li>
              <LanguageSwitchContainer />
            </li>
          </ul>
        </div>
      </nav>
      <div className={styles["responsive-order"]}>
        <div className="uk-container">
          <nav className={styles["nav-bar__bottom"]}>
            <div className={styles["nav-bar__bottom-logo"]}>
              <Link to="./">
                <img
                  className={styles["nav-bar__bottom-logo__image"]}
                  src={props.navBarModule.first_logo}
                  alt="image"
                />
                <img
                  className={styles["nav-bar__bottom-logo__text"]}
                  src={props.navBarModule.second_logo}
                  alt="image"
                />
              </Link>
            </div>
            <ul className={`${styles[`nav-bar__bottom-menu`]} ${styles[isMenuOpened ? "responsive" : ""]}`}>
              {props.navBarModule.botLinks.map((link, index) => (
                <li key={index}>
                  <Link to={link.route}>{link.content}</Link>
                </li>
              ))}
              <li
                role="li"
                onClick={() => {
                  setIsSearching(true);
                }}
                className={styles["nav-bar__bottom__search"]}
              >
                <img src={SEARCH_ICON} alt="Image" />
              </li>
            </ul>
          </nav>
        </div>
      </div>
      <NavbarSearchComponent opened={isSearching} onClose={stopSearching} />
    </div>
  );
};

export { NavBarComponent };
