import React, { useState } from "react";
import { Button } from "@app/modules/core";
import ChevronDown from "@assets/icons/chevron-down.svg";
import { FilterOptions } from "@app/api";

interface IFilterProps {
  filterOptions: FilterOptions[];
  onFilterChanged: (value: string) => void;
  title: string;
  showMoreTitle: string;
  maxDisplay: number;
}

const FilterComponent = (props: IFilterProps) => {
  const MAX_FILTER_OPTIONS = props.maxDisplay;

  const [showItems, setShowItems] = useState({ items: MAX_FILTER_OPTIONS, isOpen: true });
  const showMore = props.filterOptions && props.filterOptions.length > showItems.items;

  function toggleHeight() {
    if (showItems.isOpen) {
      setShowItems({ items: props.filterOptions.length, isOpen: !showItems.isOpen });
    } else {
      setShowItems({ items: MAX_FILTER_OPTIONS, isOpen: !showItems.isOpen });
    }
  }

  return (
    <React.Fragment>
      <div className="form__title">
        <h4>{props.title}</h4>
      </div>
      {props.filterOptions && (
        <ul className="form__list">
          {props.filterOptions.slice(0, showItems.items).map((item: FilterOptions, index: number) => (
            <li key={index}>
              <input
                aria-checked={!!item.value} // add logic here to check or uncheck
                className="form__checkbox"
                id={item.content}
                type="checkbox"
                onClick={() => {
                  props.onFilterChanged(item.content);
                }}
              />

              <label className="form__label" htmlFor={item.content}>
                {item.content}
              </label>
            </li>
          ))}
        </ul>
      )}
      {showMore && (
        <div className="form__bottom">
          <Button icon={ChevronDown} title={props.showMoreTitle} variant={"default"} onClick={toggleHeight} />
        </div>
      )}
      {!showItems.isOpen && (
        <div className="form__bottom">
          <Button icon={ChevronDown} title="Sluiten" variant={"default"} onClick={toggleHeight} />
        </div>
      )}
    </React.Fragment>
  );
};

export { FilterComponent };
