import * as React from "react";
import styles from "./product-card-component.module.scss";
import { Link } from "react-router-dom";
import { ProductCard } from "@app/api";
export interface IProductCardComponentProps {
  productCard: ProductCard;
}

const ProductCardComponent = (props: IProductCardComponentProps) => (
  <Link to="" className={`${styles[`card`]} ${styles[`card--product`]} `}>
    <div className={styles[`card__media`]}>
      <div className={styles[`card__image`]}>
        <img src={props.productCard.image} alt={props.productCard.title} />
      </div>
    </div>

    <div className={styles[`card__body`]}>
      <div className={styles[`card__head`]}>
        <div className={styles[`card__title`]}>{props.productCard.title}</div>
        <div className={styles[`card__content`]}>{props.productCard.content}</div>
      </div>
    </div>
  </Link>
);

export { ProductCardComponent };
